import React,{useState,useEffect} from 'react'
import { Button, Form } from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import Popup from 'reactjs-popup'
import { getNow } from '../../assets/helpers/Date'
import { getAllClients } from '../../services/Clients'
import { getOneMachine, updateMachine } from '../../services/Machines'
import Loading from '../Loading'


const ModifNewMachine = () => {
    const [openModalValid,setOpenModalValid] = useState(false)
    const [loading,setLoading] = useState(false)
    const [error,setError] = useState()
    const [id,setId] = useState()
    const history = useHistory()
    const [clients,setClients] = useState()
    const [machine,setMachine] = useState( 
        {
        "machine_id" : "",
        "constructeur" : "",
        "nom":"",
        "numero_serie":"",
        "type":"",
        "client":"",
        "new_machine":"1",
        "created_at":"",
        "updated_at":""
    }
    )

    useEffect(()=>{
        var url = window.location.pathname
        url = url.split("/")
        url = url[url.length - 1]
        console.log(url)
        setId(url)
   },[])

   useEffect(()=>{
       if(id){
        setLoading(true)
        getMachine()
        getClient()
       }
       
   },[id])

   const getMachine = async () => {
       let resp = await getOneMachine(id)
       setMachine(resp[0])
   }

   const getClient = async () => {
       let resp = await getAllClients()
        setClients(resp)
        setLoading(false)
   }

   const handleChange = ({currentTarget}) => {
    setMachine({...machine,[currentTarget.name]:currentTarget.value})
   }

   const handleAnnul = () => {
        history.push("/")
   }

   const sendMachine = async () => {
        if(machine.constructeur === "" || machine.nom === "" || machine.type ==="" || machine.numero_serie === "" || machine.client_id ==="" ||machine.client_id ===0){
            setError("tout les champs doivent etre remplit")
        }else{
            var arrUpdateNewMachine = {
                "machine_id" : machine.machine_id,
                "constructeur" : machine.constructeur,
                "nom":machine.nom,
                "numero_serie":machine.numero_serie,
                "type":machine.type,
                "client_id":machine.client_id,
                "new_machine":0,
                "updated_at":getNow()
            }
            console.log(arrUpdateNewMachine)
            let resp = await updateMachine(arrUpdateNewMachine)
            console.log(resp)
            if(resp == 1){
                setOpenModalValid(true)
            }else{
                setError("erreur lors de l'ajout de la machine ")
            }
        }
        
   }

   const closeModalValid = () => {
        setOpenModalValid(false)
        history.push("/")
   }

   const handleChangeSelect = (e) => {
    setMachine({...machine,["client_id"]:e.target.value})
}

   return loading ? (<Loading title={"Chargement"} />) : (
    <>
    {console.log(machine)}
   
    <Form className="formAjoutClient">
        <div className="containerRowFormClient">
            <div className="columnFormClient">
                <p>Nouvelle Machine</p>
            </div>
        </div>
        <div className="containerRowFormClient">
            <div className="columnFormClient">
                <Form.Group className="mb-3" controlId="nom">
                    <Form.Label  className="mb-3">Nom</Form.Label>
                    <Form.Control type="text" placeholder="Nom" value={machine ? machine.nom : ""} name="nom" onChange={handleChange}/>
                </Form.Group>
            </div>
            <div className="columnFormClient">
                <Form.Group className="mb-3" controlId="constructeur">
                    <Form.Label  className="mb-3">Constructeur</Form.Label>
                    <Form.Control type="text" placeholder="Constructeur" value={machine ? machine.constructeur : ""} name="constructeur" onChange={handleChange}/>
                </Form.Group>
            </div>
        </div>
        <div className="containerRowFormClient">
            <div className="columnFormClient">
            <Form.Group className="mb-3" controlId="numero_serie">
                <Form.Label  className="mb-3">Numero de série</Form.Label>
                <Form.Control type="text" placeholder="Numero de série" value={machine ? machine.numero_serie : ""} name="numero_serie" onChange={handleChange}/>
            </Form.Group>
            </div>
            <div className="columnFormClient">
                <Form.Group className="mb-3" controlId="type">
                    <Form.Label  className="mb-3">Type</Form.Label>
                    <Form.Control type="text" placeholder="Type" value={machine ? machine.type : ""} name="type" onChange={handleChange}/>
                </Form.Group>
            </div>
        </div>
        <div className="containerRowFormClient">
            {clients ? (<div className="columnFormClient">
                <p>Client : </p>
                        <Form.Group className="mb-3" controlId="client">
                            <select value={machine.client_id} onChange={handleChangeSelect}>
                                <option key='0' value='0'>client</option>
                            {clients.map((client) => (
                                <option key={client.client_id} value={client.client_id}>{client.societe}</option>
                            ))}
                            </select>
                        </Form.Group>
                    </div> ) : "" }
        </div>
        {error ? (<div className="containerRowFormClientError">
            <p>{error}</p>
        </div>) : ""}
        <div className="containerRowFormClientButton">
            <div className="columnFormClientButton">
                <Button onClick={handleAnnul}>Annuler</Button>
                <Button onClick={sendMachine}>Ajouter</Button>
            </div>
        </div>
    </Form>
    <Popup open={openModalValid} closeOnDocumentClick onClose={closeModalValid}>
        <div className="containerClienPopUp">
            <div>
                <p>Machine ajouté avec succès</p>
            </div>
            <div className="containerBouttonPopUp">
                <Button onClick={closeModalValid}>Fermer</Button>
            </div>
        </div>
    </Popup>
</>
)
}

export default ModifNewMachine
