import React,{useRef,useState} from 'react'
import ReactDOM from 'react-dom'
import Popup from "reactjs-popup";
import SignaturePad from "react-signature-canvas";
import {Button} from 'react-bootstrap'
import { useEffect } from 'react';
import '../../css/Signature.css'
import { useHistory } from 'react-router-dom';
import 'reactjs-popup/dist/index.css';
import { getItem } from '../../services/localStorage';
import { updateUser } from '../../services/Users';
import {getNow} from '../../assets/helpers/Date'

const ModifSignature = () => {
    const history = useHistory()
    const [open, setOpen] = useState(false); 
    const closeModal = () => setOpen(false);
    const sigCanvas = useRef({});
    const [imageURL, setImageURL] = useState(null);
    const [error,setError] = useState("")
    const save = () =>{
        setImageURL(sigCanvas.current.getTrimmedCanvas().toDataURL("image/png"));
        setOpen(o => !o)
    }
    const clear = () => {
        sigCanvas.current.clear();
        setImageURL(null)
    }
    const annuler = (e) => {
        e.preventDefault()
        history.replace("/profil")
    }
    const validModif = async (e) =>{
        e.preventDefault()
        var updateSignature = {"id":getItem("userId"),"signature":imageURL,"updated_at":getNow()}
        let resp = await updateUser(updateSignature)
        
        if(resp == 1){
            history.replace("/profil")
        }else{
            closeModal()
            setError("Erreur de modification")
        }
    }
    
    return (
        <>
        <div className="containerSignature">
            <div className="signature">
                <p className="titleSignature">Signature : </p>

                    <SignaturePad ref={sigCanvas} canvasProps={{ className: "signatureCanvas" }} />
                    <div className="containerButton">
                        <Button className="m-2 buttonSignature" onClick={save}>Modifier</Button>
                        <Button className="m-2 buttonSignature" onClick={clear}>Supprimer</Button>
                        <Button className="m-2 buttonSignature" onClick={annuler}>Annuler</Button>
                    </div>
                    <div className="errorSignature">{error}</div>
                </div>
        </div>
        
        <Popup open={open} closeOnDocumentClick onClose={closeModal}>
            <div className="test"><p>Votre Signature : </p><img src={imageURL} alt="my signature"/>
                <div className="containerBouttonPopUpModifSignature">
                    <Button onClick={validModif}>Valider</Button>
                    <Button onClick={closeModal}>Annuler</Button>
                </div>
            </div>
        </Popup>
        </>
    )
}   

export default ModifSignature