export async function getBiOnProdWithUser(id){
    return await fetch('https://www.emanorcloud.fr/api/v2/bi/searchByOnProd/'+id, {
             method: 'GET',
             mode: "cors",
             headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
             .then(response => {
                     if(response.status === 204){
                             return 0
                     }else{
                             return response.json()
                     }
                })
}

