export async function getAllBi(){
        return await fetch('https://www.emanorcloud.fr/api/v2/bi', {
                 method: 'GET',
                 mode: "cors",
                 headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
                 .then(response => {
                         if(response.status === 204){
                                 return 0
                         }else{
                                 return response.json()
                         }
                    })
    }
    

    export async function postBi(arrBi){
        return await fetch('https://www.emanorcloud.fr/api/v2/bi', {
                 method: 'POST',
                 mode: "cors",
                 body : JSON.stringify(arrBi),
                 headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
                 .then(response => {
                         if(response.status === 400){
                                 return 0
                         }else if (response.status === 402)
                         {
                                 return 1
                         }else{
                                return 2
                         }
                    })
    }

