import React,{useState,useEffect} from 'react'
import { archiveUser, getAllUsers, supprUser } from '../../services/Utilisateurs'
import { Table, Form, Button } from 'react-bootstrap'
import '../../css/Utilisateurs.css'
import UtilisateurRow from '../../components/UtilisateurRow'
import Loading from '../../components/Loading'
import Popup from 'reactjs-popup'
import { updateUser } from '../../services/Users'


const ContainerUtilisateurArchive = () => { 
    const [utilisateurs,setUtilisateurs] = useState()
    const [loading, setLoading] = useState(true)
    const [openValidDesarchive, setOpenValidDesarchive] = useState(false)
    const [errorDesarchive, setErrorDesarchive] = useState()
    useEffect(()=>{
        async function getUtilisateursArchive(){
            var resp = await archiveUser()
            setUtilisateurs(resp)
            setLoading(false)
        }
        getUtilisateursArchive()
    },[])

    const desarchiveUser = async (id) => {
        setLoading(true)
        let arUs = {"id":id,"deleted_at":null}
        let resp = await updateUser(arUs)
        if(resp === 1){
            setLoading(true)
            let resp = await archiveUser()
            setUtilisateurs(resp)
            setLoading(false)
        }else{
            setErrorDesarchive("Erreur lors du desarchivage de l'utilisateur")
            setOpenValidDesarchive(true)
            setLoading(false)
        }
    }
    const closeModalDesarchive = () => {
        setOpenValidDesarchive(false)
    }

    return loading ? (<Loading title={"Chargement"}/>) : ( utilisateurs === 0  ? (
        <div className="containerUtilisateurs">
            <div className="containerTableUtilisateurs">
                <p className="textNoUser">Aucun utilisateur archivé</p>
            </div>
        </div>
        ) : (
        <div className="containerUtilisateurs">
            <div className="containerTableUtilisateurs">
                <Table responsive>
                    <thead>
                        <tr className="ligneTableau">
                            <th>Nom de famille</th>
                            <th>Prénom</th>
                            <th>Login</th>
                            <th>Niveau d'utilisateur</th>
                            <th>Créer le</th>
                            <th>Dernière modification</th>
                            <th>Date de suppression</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {utilisateurs ? utilisateurs.map(user => <UtilisateurRow key={user.user_id} user={user} desarchiveUser={desarchiveUser}/>) : <tr key={"1"}><td></td></tr>}
                    </tbody>
                </Table>
                <Popup open={openValidDesarchive} closeOnDocumentClick onClose={closeModalDesarchive}>
                <div className="containerMdp">
                    <p>{errorDesarchive ? errorDesarchive : "utilisateur desarchiver avec succès"}</p>                   
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalDesarchive}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            </div>
        </div>
    ))
}

export default ContainerUtilisateurArchive