import React,{useState,useEffect} from 'react'
import { getAllUsers, supprUser } from '../services/Utilisateurs'
import { Table, Form, Button } from 'react-bootstrap'
import '../css/Utilisateurs.css'
import UtilisateurRow from '../components/UtilisateurRow'
import Loading from '../components/Loading'
import Popup from 'reactjs-popup'
import { getItem } from '../services/localStorage'


const UtilisateurVisu = ({users, setModif}) => {
    const [utilisateurs,setUtilisateurs] = useState(users)
    const [loading, setLoading] = useState(false)
    const [openValidSuppr, setOpenValidSuppr] = useState(false)
    const [errorSuppr, setErrorSuppr] = useState()

    const suppressionUser = async (id) => {
        setLoading(true)
        if(id === getItem("userId")){
            setErrorSuppr("Vous ne pouvez pas archiver votre propre profil")
            setOpenValidSuppr(true)
            setLoading(false)
        }else{
            let respSuppr = await supprUser(id)
            if (respSuppr === 1){
                let resp = await getUsers()
                setUtilisateurs(resp)
                setOpenValidSuppr(true)
                setLoading(false)
            }else{
                setErrorSuppr("erreur lors de la suppresion de l'utilisateur")
                setOpenValidSuppr(true)
                setLoading(false)
            }
        }
    }
    const closeModalSuppr = () => {
        setOpenValidSuppr(false)
    }
    const getUsers = async () => {
        return await getAllUsers()
    }
    const openModifUtilisateur = (user) => {
        setModif(user)
    }

    return loading ? (<Loading title={"Chargement"}/>) : (
        <div className="containerUtilisateurs">
            <div className="containerTableUtilisateurs">
                <Table responsive>
                    <thead>
                        <tr className="ligneTableau">
                            <th>Nom de famille</th>
                            <th>Prénom</th>
                            <th>Login</th>
                            <th>Niveau d'utilisateur</th>
                            <th>Créer le</th>
                            <th>Dernière modification</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {utilisateurs ? utilisateurs.map(user => <UtilisateurRow key={user.user_id} user={user} suppressionUser={suppressionUser} openModifUtilisateur={openModifUtilisateur}/>) : <tr key={"1"}><td></td></tr>}
                    </tbody>
                </Table>
                <Popup open={openValidSuppr} closeOnDocumentClick onClose={closeModalSuppr}>
                <div className="containerMdp">
                    <p>{errorSuppr ? errorSuppr : "utilisateur supprimer avec succès"}</p>                   
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalSuppr}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            </div>
        </div>
    )
}

export default UtilisateurVisu