import React, { useEffect,useState } from 'react' 
import { Button } from 'react-bootstrap'
import { getItem } from '../../services/localStorage'
import PopUpForfaitClient from './PopUpForfaitClient'
import PopUpVisuClient from './PopUpVisuClient'
 

const ClientRow = ({client,modifyClient,delClient,desarchClient}) => {
    
    const [openModClient, setOpenModClient] = useState(false)
    const [openModForfaitClient, setOpenModForfaitClient] = useState(false)
    const openModalVisuClient = () => {
        setOpenModClient(true)
    }
    const openModalForfaitClient = () => {
        setOpenModForfaitClient(true)
    }
    const closeModalForfaitClient = () => {
        setOpenModForfaitClient(false)
    }
    const closeModalVisuClient = () => {
        setOpenModClient(false)
    }

    const deleteClient = (id) => {
        delClient(id)
    }
    const desarchiveClient = (id) => {
        desarchClient(id)
    }
    return(
        <>
        {console.log(getItem('userRole'))}
            <tr className="ligneTableau">
                <td>{client.societe}</td>
                <td>{client.interlocuteur}</td>
                <td>{client.numero_tel}</td>
                <td>{client.adresse_mail}</td>
                <td><Button className="m-2 btnVisu" onClick={openModalForfaitClient}><i className="tiny material-icons">remove_red_eye</i></Button></td>
                <td>{client.created_at}</td>
                <td>{client.updated_at}</td>
                {client.deleted_at ? <td>{client.deleted_at}</td> : null}
                <td>
                    <Button className="m-2 btnVisu" onClick={openModalVisuClient}><i className="tiny material-icons">remove_red_eye</i></Button>
                   
                </td>
            </tr>
            <PopUpVisuClient open={openModClient} closeModalVisuClient={closeModalVisuClient} client={client} />
            <PopUpForfaitClient  openForfaitClient={openModForfaitClient} closeModalForfaitClient={closeModalForfaitClient} client={client} />
        </>
    )
}

export default ClientRow