import React,{useState} from "react";
import { useEffect } from "react";
import {Form, Button} from 'react-bootstrap'
import { useHistory } from "react-router-dom";
import { getNow } from "../assets/helpers/Date"; 
import '../css/FormModifUser.css'

const FormModifUser = ({user, updatedUser}) => { 
    const [error,setError] = useState("")
    const history = useHistory()
    const [userModif, setUserModif] = useState(user)

    const handleChange = ({currentTarget}) =>{
        setUserModif({...userModif,[currentTarget.name]:currentTarget.value})
    }
    const handleSubmit = async (e) => {
        e.preventDefault();
        let user = {"id":userModif.user_id,"login":userModif.login,"firstname":userModif.firstname,"lastname":userModif.lastname,"updated_at":getNow()}
        updatedUser(user)
    }

    const handleClickAnnul = (e) =>{
        e.preventDefault()
        history.replace("/profil")
    }
    return(
        <div className="containerFormModif">
            <Form className="formModif" onSubmit={handleSubmit}>
                    <Form.Group className="mb-3 titleLogin" controlId="lastname">
                        <Form.Label className="mb-3">Nom de famille</Form.Label>
                        <Form.Control type="text" placeholder="Nom de famille" value={userModif ? userModif.lastname : user.lastname} className="inputLogin" name="lastname" onChange={handleChange}/>
                    </Form.Group>

                    <Form.Group className="mb-3 titleLogin" controlId="firstname">
                        <Form.Label className="mb-3">Prénom</Form.Label>
                        <Form.Control type="text" placeholder="Prénom" className="inputLogin" value={userModif ? userModif.firstname : user.firstname} name="firstname" onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group className="mb-3 titleLogin" controlId="login">
                        <Form.Label  className="mb-3">Login</Form.Label>
                        <Form.Control type="text" placeholder="Login"className="inputLogin" value={userModif ? userModif.login : user.login} name="login" onChange={handleChange}/>
                    </Form.Group>
                    <div  className="errorLogin">{error ? error : ""}</div>
                    <div className="containerButtonLogin mt-4 ">
                        <Button type="submit" className="buttonMdp" onClick={handleClickAnnul}>
                            Annuler
                        </Button>
                        <Button type="submit"  className="buttonConnexion">
                            Modifier
                        </Button>
                    </div>
                </Form>
        </div>
    )
}

export default FormModifUser