import React,{useState,useEffect} from 'react'
import { Table } from 'react-bootstrap'
import BiRow from '../../components/bi/BiRow'
import BiRowArchive from '../../components/bi/BiRowArchive'
import Loading from '../../components/Loading'
import { getArchive, updateBi } from '../../services/Bi'

const BiArchive = () =>{
    const [listBiArchive,setListBiArchive] = useState([])
    const [loading,setLoading] = useState(false)

    useEffect(()=>{
        setLoading(true)
        getBiArchive()
    },[])

    const getBiArchive = async () => {
        let resp = await getArchive()
        setListBiArchive(resp)
        setLoading(false)
    }

    const restore = async (id) => {
        let arrDesarchive = {
            "bi_id":id,
            "deleted_at":null
        }

        let resp = await updateBi(arrDesarchive)
        if(resp === 2){
            setLoading(true)
            getBiArchive()
        }

    }
    return loading ? (<Loading title={"chargement"} />) : (
        
            <div className="containerMachine">
            <div className='containerTableUtilisateurs'>
                    {!listBiArchive ? (<div><p className='fontTitleRowVisuBi'>Aucun Bi archivé</p></div>) : (
                        <div className="containerTable">
                    <Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Numero Bon Intervention</th>
                                <th>Client</th>
                                <th>Machine</th>
                                <th>Bi en prodution</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {listBiArchive ? listBiArchive.map(bi => <BiRowArchive key={bi.bi_id} bi={bi} restore={restore} />) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>
                </div>
            
        
                    )}
            </div></div>
    )
}

export default BiArchive