export function getNow(){
    var now = new Date();
        var year = now.getFullYear();
        var month = now.getMonth();
        var day = now.getDate()
        var heure = now.getHours();
        var minute = now.getMinutes();
        var date = year + "-" + (month + 1) + "-" + day + " " + heure + ":" + minute + ":00"
        return date
}