import React,{useState,useEffect} from 'react' 
import { Form, Button,FloatingLabel } from 'react-bootstrap'
import Popup from 'reactjs-popup'
import {registerUser} from '../services/Utilisateurs'
import PopupMdp from './PopupMdp'
import Loading from '../components/Loading'
import '../css/FormUtilisateur.css'
import { getNow } from '../assets/helpers/Date'
import { updateUser } from '../services/Users'
const FormUtilisateur = ({user,retourPage}) => {
    const [utilisateur, setUtilisateur] = useState(
        {
            "user_id": "",
            "firstname":"",
            "lastname" : "",
            "login" : "",
            "lvlUser_id" : 1,
            "signature" : "path"
        }
    )
    const [loading,setLoading] = useState(false)
    const [error, setError] = useState("")
    const [errorMdp, setErrorMdp] = useState()
    const [mdp,setMdp] = useState("")
    const [mdpConf, setMdpConf] = useState("")
    const [openPopUpMessage, setOpenPopUpMessage] = useState(false)
    const [messageValidCrea, setMessageValidCrea] = useState("")
    const [lvlUser,setLvlUser] = useState(
        [
            [1,"Technicien"],
            [2,"Responsable Technique"],
            [3,"Directeur"],
            [4,"Administrateur"]
        ]
    )
    const [openModalMdp,setOpenModalMdp] = useState(false)
    useEffect(()=> {
        if(user){
            console.log(user)
            setUtilisateur(user)
        }
    },[])
    const handleSubmitCreation = async () => {
        setErrorMdp("")
        setError('')
        console.log(utilisateur)
        if(utilisateur.firstname === "" || utilisateur.lastname === "" || utilisateur.login === ""
        || utilisateur.lvlUser_id === "" || mdp === ""|| mdpConf === ""){
            setError("Des données sont manquantes")
        }else{
            if(mdp != mdpConf){
                setErrorMdp('les deux mots de passe sont différent')
                setMdpConf("")
            }else{
                setLoading(true)
                let arUsCrea = {"firstname":utilisateur.firstname,"lastname":utilisateur.lastname,
                "login":utilisateur.login,"password":mdp,'password_confirmation':mdpConf,"signature":"path",'lvlUser_id' : utilisateur.lvlUser_id}
                let resp = await registerUser(arUsCrea)

                if(resp.id){
                    setMessageValidCrea("Utilisateur créer avec succès")
                    setOpenPopUpMessage(true)
                    setUtilisateur({
                        "user_id": "",
                        "firstname":"",
                        "lastname" : "",
                        "login" : "",
                        "lvlUser_id" : 1,
                        "signature" : "path"
                    })
                    setMdp("")
                    setMdpConf("")
                }else if(resp===0){
                    setMessageValidCrea("Utilisateur deja créer")
                    setOpenPopUpMessage(true)
                    setUtilisateur({
                        "user_id": "",
                        "firstname":"",
                        "lastname" : "",
                        "login" : "",
                        "lvlUser_id" : 1,
                        "signature" : "path"
                    })
                    setMdp("")
                    setMdpConf("")
                }else{
                    setMessageValidCrea("Erreur lors de la création de l'utilisateur")
                    setOpenPopUpMessage(true)
                }
                setLoading(false)
            }
        }
    }
    const handleSubmitModification = async () =>{
        if(utilisateur.firstname === "" || utilisateur.lastname === "" || utilisateur.login === ""
        || utilisateur.lvlUser_id === ""){
            setError("Des données sont manquantes")
        }else{
            setLoading(true)
            var arrUpUtilisateur = {"id":utilisateur.user_id,"lastname":utilisateur.lastname,"firstname":utilisateur.firstname,
            "login":utilisateur.login,"lvlUser_id":utilisateur.lvlUser_id,"updated_at":getNow()}
            let resp = await updateUser(arrUpUtilisateur)
            console.log(resp)
            if(resp != 1){
                setMessageValidCrea("Erreur lors de la mise a jour de l'utilisateur")
                setOpenPopUpMessage(true)
            }else{
                setMessageValidCrea("Utilisateur modifié avec succès")
                setOpenPopUpMessage(true)
            }
            setLoading(false)
        }
        console.log(arrUpUtilisateur)
    }
    const handleChange = ({currentTarget}) => {
        setUtilisateur({...utilisateur,[currentTarget.name]:currentTarget.value})
    }
    const handleClickAnnul = (e) => {
        e.preventDefault()
        retourPage()
    }
    const handleChangeMdp = ({currentTarget}) => {
        setMdp(currentTarget.value)
    }
    const handleChangeMdpConf = ({currentTarget}) => {
        setMdpConf(currentTarget.value)
    }
    const handleChangeLvlUser = ({currentTarget}) => {
        setUtilisateur({...utilisateur,"lvlUser_id":currentTarget.value})
    }
    
    const openMdp = () => {
        setOpenModalMdp(true)
    }

    const closeMdp = () => {
        setOpenModalMdp(false)
    }

    const closePopUpMessageFormUser = () => {
        setOpenPopUpMessage(false)
    }

    useEffect(()=>{
        setOpenModalMdp(openModalMdp)
    },[openModalMdp])

    return loading ? (<Loading title={"chargement"} />) : (
    <div className="containerFormModif">
    <Form className="formModif" >
            <Form.Group className="mb-3 titleLogin" controlId="lastname">
                <Form.Label className="mb-3">Nom de famille</Form.Label>
                <Form.Control type="text" placeholder="Nom de famille" value={utilisateur ? utilisateur.lastname : ""} className="inputLogin" name="lastname" onChange={handleChange}/>
            </Form.Group>

            <Form.Group className="mb-3 titleLogin" controlId="firstname">
                <Form.Label className="mb-3">Prénom</Form.Label>
                <Form.Control type="text" placeholder="Prénom" className="inputLogin" value={utilisateur ? utilisateur.firstname : ""} name="firstname" onChange={handleChange}/>
            </Form.Group>
            <Form.Group className="mb-3 titleLogin" controlId="login">
                <Form.Label  className="mb-3">Login</Form.Label>
                <Form.Control type="text" placeholder="Login"className="inputLogin" value={utilisateur ? utilisateur.login : ""} name="login" onChange={handleChange}/>
            </Form.Group>
            
            <Form.Group className="mb-3 titleLogin" controlId="lvlUser">
                <Form.Label  className="mb-3">Niveau de l'utilisateur</Form.Label>
                <Form.Select aria-label="lvlUser" value={utilisateur.lvlUser_id} size="lg" onChange={handleChangeLvlUser} name="lvlUser">
                    {
                        lvlUser.map((lvl)=>{
                            if(utilisateur.lvlUser_id === lvl[0]){
                                return <option key={lvl[0]} value={lvl[0]}>{lvl[1]}</option>
                            }else{
                                return <option key={lvl[0]} value={lvl[0]}>{lvl[1]}</option>
                            }
                        })
                    }
                </Form.Select>
            </Form.Group>
            {utilisateur.user_id === "" ? 
                <><Form.Group className="mb-3 titleLogin" controlId="mdp">
                    <Form.Label  className="mb-3">Mot de passe</Form.Label>
                    <Form.Control type="password" placeholder="mot de passe"className="inputLogin" value={mdp ? mdp : ""} name="mdp" onChange={handleChangeMdp}/>
                </Form.Group>
                <Form.Group className="mb-3 titleLogin" controlId="mdpConf">
                    <Form.Label  className="mb-3">Confirmation du mot de passe</Form.Label>
                    <Form.Control type="password" placeholder="confirmation mot de passe"className="inputLogin" value={mdpConf ? mdpConf : ""} name="mdpConf" onChange={handleChangeMdpConf}/>
                </Form.Group>
                <div className='errorMdp'>{errorMdp ? errorMdp : ""}</div></>
                : <div className="mdpModif"><span>Mot de passe : </span><Button onClick={openMdp}><i className="material-icons">edit</i></Button></div>}
            <div  className="errorLogin">{error ? error : ""}</div>
            <div className="containerButtonLogin mt-4 ">
                <Button type="submit" className="buttonMdp" onClick={handleClickAnnul}>
                    Annuler
                </Button>
                {utilisateur.user_id === "" ? <Button className='buttonConnexion' onClick={handleSubmitCreation}>Créer</Button> : <Button className='buttonConnexion' onClick={handleSubmitModification}>Modifier</Button>}
            </div>
        </Form>
        <PopupMdp open={openModalMdp} utilisateur={utilisateur} modalMdpClose={closeMdp}/>
        <Popup open={openPopUpMessage} closeOnDocumentClick onClose={closePopUpMessageFormUser}>
                <div className='messagePopUp'>
                    {messageValidCrea}
                </div>
                <div className="buttonMessage">
                    <Button onClick={closePopUpMessageFormUser}>Fermer</Button> 
                </div>
        </Popup>
</div>)
}

export default FormUtilisateur