import React from 'react'
import { Button } from 'react-bootstrap'

const NewMachineRow = ({machine,validMachine,modifyMachine}) => {

    return(
        <>
            <tr className="ligneTableau">
                <td>{machine.nom}</td>
                <td>{machine.constructeur}</td>
                <td>{machine.numero_serie}</td>
                <td>{machine.type}</td>
                <td>{machine.client[0].societe}</td>
                <td>
                    <Button className="m-2 btnVisu" onClick={()=>validMachine(machine.machine_id)}><i className="tiny material-icons">add</i></Button>
                    <Button className="m-2 btnEdit" onClick={()=>{modifyMachine(machine)}}><i className="material-icons">edit</i></Button>
                </td>
            </tr>
        </>
    )
}

export default NewMachineRow

