import React,{useEffect, useState} from 'react' 
import Popup from "reactjs-popup";
import { Button } from 'react-bootstrap';

const PopUpVisuMachine = ({open,machine,closeModalVisuMachine}) => {

    const [openModMachine,setOpenModMachine] = useState(false)
 
    useEffect(()=>{
        setOpenModMachine(open)
    },[open])


    return(
        <Popup open={openModMachine} closeOnDocumentClick onClose={closeModalVisuMachine}>
                <div className="containerMachinePopUp">
                    <p className='fontBold fontTitle'>Machine </p>                   
                    <div className="containerClientOnPopUp">
                        <p><span className='fontBold fontTitleName'>Nom :</span> {machine.nom}</p>
                        <p><span className='fontBold fontTitleName'>Constructeur :</span> {machine.constructeur}</p>
                        <p><span className='fontBold fontTitleName'>Numéro de série :</span> {machine.numero_serie}</p>
                        <p><span className='fontBold fontTitleName'>Type :</span> {machine.type}</p>
                        <p>Créer le : {machine.created_at} Modifier le : {machine.updated_at}</p>
                    </div>
                    
                    <div className="dataUtilisateurPopUp">{machine.data.map((d) =>  <p key={d.machineData_id}>{d.key} : {d.value}</p>)}</div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalVisuMachine}>Fermer</Button>
                    </div>
                </div>
            </Popup>
    )
}

export default PopUpVisuMachine