import React,{useState,useEffect} from 'react'
import { FloatingLabel, Form, Table, Button} from 'react-bootstrap'
import { getAllClients, getOneClient } from '../../services/Clients'
import Loading from '../../components/Loading'
import { getMachineByClient, getOneMachine } from '../../services/Machines'
import '../../css/Bi.css'
import { useHistory } from 'react-router-dom';
import FournitureRow from '../../components/bi/FournitureRow'
import { isNumb, strRandom } from '../../assets/helpers/general'


const BiAdd = () =>{
    const history = useHistory()
    const [openPhoto,setOpenPhoto] = useState(false)
    const [error,setError] = useState()
    const [loading,setLoading] = useState(false)
    const [isClient,setIsClient] = useState(true)
    const [listClient,setListClient] = useState()
    const [clientSelectId,setClientSelectId] = useState()
    const [client,setClient] = useState()
    const [listMachine,setListMachine] = useState([])
    const [machineSelect,setMachineSelect] = useState()
    const [machine,setMachine] = useState()
    const[forfaitClient,setForfaitClient] = useState()
    const [bi,setBi] = useState({
        "numero_bi" : strRandom(),
        "client_id":"0",
        "machine_id":"0",
        "travail_a_faire":"",
        "numero_bl" : "",
        "prix_total_fourniture" : 0,
        'fourniture' : [],
        "prix_total_forfait" : 0,
        "constatation_arrivee" : "",
        "travaux_realise" : "",
        "observation_client" : ""
    })
    var prixTotalFourniture = 0
    const [openListMachine,setOpenListMachine] = useState(true)
    const [nombreFourniture,setNombreFourniture] = useState([])
    const [fourniture,setFourniture] = useState([])
    const [forfaitLundi,setForfaitLundi] = useState({
            "jour" : "lundi",
            "date" : "",
            "nombre_deplacement" : 0,
            "nombre_heure" : 0,
            "repas" : 0,
            "hotel" : 0
    })
    const [forfaitMardi,setForfaitMardi] = useState({
        "jour" : "mardi",
        "date" : "",
        "nombre_deplacement" : 0,
        "nombre_heure" : 0,
        "repas" : 0,
        "hotel" : 0
    })
    const [forfaitMercredi,setForfaitMercredi] = useState({
        "jour" : "mercredi",
        "date" : "",
        "nombre_deplacement" : 0,
        "nombre_heure" : 0,
        "repas" : 0,
        "hotel" : 0
    })
    const [forfaitJeudi,setForfaitJeudi] = useState({
        "jour" : "jeudi",
        "date" : "",
        "nombre_deplacement" : 0,
        "nombre_heure" : 0,
        "repas" : 0,
        "hotel" : 0
    })
    const [forfaitVendredi,setForfaitVendredi] = useState({
        "jour" : "vendredi",
        "date" : "",
        "nombre_deplacement" : 0,
        "nombre_heure" : 0,
        "repas" : 0,
        "hotel" : 0
    })
    
    const [prixTotalForfaitLundi,setPrixTotalForfaitLundi] = useState(0)
    const [prixTotalForfaitMardi,setPrixTotalForfaitMardi] = useState(0)
    const [prixTotalForfaitMercredi,setPrixTotalForfaitMercredi] = useState(0)
    const [prixTotalForfaitJeudi,setPrixTotalForfaitJeudi] = useState(0)
    const [prixTotalForfaitVendredi,setPrixTotalForfaitVendredi] = useState(0)
    const [photos,setPhotos] = useState({
        ["photo1"]:{},
        ["photo2"]:{},
        ["photo3"]:{}
    })
    useEffect (() => {
        setLoading(true)
        getClients();
        
    },[])

    useEffect (()=>{
        getMachine()
        getForfaitClient()
    },[clientSelectId])

    useEffect(()=>{
        var forfait_dep = 50
        var forfait_heure = 65
        var forfait_hotel = 120
        var forfait_repas = 25
        if(client){
            
            if(client.forfait_dep !== ""){
                forfait_dep = client.forfait_dep
            }
            if(client.forfait_heure !== ""){
                forfait_heure = client.forfait_heure
            }
            if(client.hotel !== ""){
                forfait_hotel = client.hotel
            }
            if(client.repas !== ""){
                forfait_repas = client.repas
            }
            setIsClient(false)
        }else{
            setIsClient(true)
            setBi({...bi,["prix_total_forfait"]:0})
        }
        
        setForfaitClient({
            "forfait_dep" : forfait_dep,
            "forfait_heure" : forfait_heure,
            "forfait_repas" : forfait_repas,
            "forfait_hotel" : forfait_hotel
        })
       
    },[client])

    useEffect(()=>{
        if(machineSelect){
            
            oneMachine(machineSelect)
        }
    },[machineSelect])

    useEffect(()=>{
        var prixT = prixTotalForfaitLundi + prixTotalForfaitMardi + prixTotalForfaitMercredi+prixTotalForfaitJeudi+prixTotalForfaitVendredi
        setBi({...bi,["prix_total_forfait"] : prixT})
    },[prixTotalForfaitLundi,prixTotalForfaitMardi,prixTotalForfaitMercredi,prixTotalForfaitJeudi,prixTotalForfaitVendredi])

   const oneMachine = async (id) => {
       if(machineSelect){
        let resp = await getOneMachine(id)
        setMachine(resp[0])
       }
        
   }
    const getForfaitClient = async () => {
        if(clientSelectId){
            let resp = await getOneClient(clientSelectId)
            setClient(resp[0])
        }
    }
    

    const getClients = async () => {
        let resp = await getAllClients()
        setListClient(resp)
        setLoading(false)
    }

    const getMachine = async () => {
        if(clientSelectId){
            let resp = await getMachineByClient(clientSelectId)
            if(resp === 0){
                setListMachine([])
                setBi({...bi,["machine_id"]:"0"})
                setOpenListMachine(true)
            }else{
                setListMachine(resp)
                setOpenListMachine(false)
            }
           
        } 
    }

    const handleChange = ({currentTarget}) => {
        setBi({...bi,[currentTarget.name]:currentTarget.value})
    }

    const handleChangeSelectClient = (e) => {
        setMachineSelect()
        setListMachine([])
        setClientSelectId(e.target.value)
        setBi({...bi,['client_id']:e.target.value})
    }

    const handleChangeSelectMachine = (e) => {
        setMachineSelect(e.target.value)
        setBi({...bi,['machine_id']:e.target.value})
    }

    const handleChangeTravauxARealiser = (e) => {
        setBi({...bi,['travail_a_faire']:e.target.value})
    }

    
    const handleChangeNombreFourniture = ({currentTarget}) => {
        var nombre
        
           nombre = Number(currentTarget.value)
      
        
        if(!nombre || nombre == 0){
            nombre = 0
        }
        var arr = []
        for(let i = 0;i<nombre;i++){
            arr.push(i)
        }
        
       if(arr.length > fourniture.length){
           fourniture.push([])
           
       }else {
        
        var prixAEnelver = fourniture[arr.length]["prix_unitaire"] * fourniture[arr.length]["quantite"]
        if(!isNumb(prixAEnelver)){
            prixAEnelver = 0
        }
        prixTotalFourniture = bi['prix_total_fourniture'] - prixAEnelver
        setBi({...bi,['prix_total_fourniture']:prixTotalFourniture})
        fourniture.splice(arr.length,1)
       }
        setNombreFourniture(arr)
       
    }

 
    const ptot = (pri) => {
        prixTotalFourniture = bi['prix_total_fourniture'] + pri
        setBi({...bi,['prix_total_fourniture']:prixTotalFourniture})
    }

    const changeFourniture = (rowFourniture) => {
        fourniture[rowFourniture.index] = rowFourniture
    }

    const handleChangeForfaitLundi = ({currentTarget}) => {

        setForfaitLundi({...forfaitLundi,[currentTarget.name]:currentTarget.value})
    }
    const handleChangeForfaitMardi = ({currentTarget}) => {
        setForfaitMardi({...forfaitMardi,[currentTarget.name]:currentTarget.value})
    }
    const handleChangeForfaitMercredi = ({currentTarget}) => {
        setForfaitMercredi({...forfaitMercredi,[currentTarget.name]:currentTarget.value})
    }
    const handleChangeForfaitJeudi = ({currentTarget}) => {
        setForfaitJeudi({...forfaitJeudi,[currentTarget.name]:currentTarget.value})
    }
    const handleChangeForfaitVendredi = ({currentTarget}) => {
        setForfaitVendredi({...forfaitVendredi,[currentTarget.name]:currentTarget.value})
    }

    const onBlurForfaitLundi = () => {
        
       var pt = (forfaitLundi.nombre_deplacement * forfaitClient.forfait_dep) + (forfaitLundi.nombre_heure * forfaitClient.forfait_heure) + (forfaitLundi.hotel * forfaitClient.forfait_hotel) + (forfaitLundi.repas * forfaitClient.forfait_repas)
        setPrixTotalForfaitLundi(pt)
    }
    const onBlurForfaitMardi = () => {
        var pt = (forfaitMardi.nombre_deplacement * forfaitClient.forfait_dep) + (forfaitMardi.nombre_heure * forfaitClient.forfait_heure) + (forfaitMardi.hotel * forfaitClient.forfait_hotel) + (forfaitMardi.repas * forfaitClient.forfait_repas)
        setPrixTotalForfaitMardi(pt)

    }
    const onBlurForfaitMercredi = () => {
        var pt = (forfaitMercredi.nombre_deplacement * forfaitClient.forfait_dep) + (forfaitMercredi.nombre_heure * forfaitClient.forfait_heure) + (forfaitMercredi.hotel * forfaitClient.forfait_hotel) + (forfaitMercredi.repas * forfaitClient.forfait_repas)
        setPrixTotalForfaitMercredi(pt)

    }
    const onBlurForfaitJeudi = () => {
        var pt = (forfaitJeudi.nombre_deplacement * forfaitClient.forfait_dep) + (forfaitJeudi.nombre_heure * forfaitClient.forfait_heure) + (forfaitJeudi.hotel * forfaitClient.forfait_hotel) + (forfaitJeudi.repas * forfaitClient.forfait_repas)
        setPrixTotalForfaitJeudi(pt)

    }
    const onBlurForfaitVendredi = () => {
        var pt = (forfaitVendredi.nombre_deplacement * forfaitClient.forfait_dep) + (forfaitVendredi.nombre_heure * forfaitClient.forfait_heure) + (forfaitVendredi.hotel * forfaitClient.forfait_hotel) + (forfaitVendredi.repas * forfaitClient.forfait_repas)
        setPrixTotalForfaitVendredi(pt)

    }

    const handleChangeConstatation = ({currentTarget})=> {
        setBi({...bi,["constatation_arrivee"]:currentTarget.value})
    }

    const handleChangeTraveauRealise = ({currentTarget}) => {
        setBi({...bi,["travaux_realise"]:currentTarget.value})
    }

    const handleChangeObservationClient = ({currentTarget}) => {
        setBi({...bi,["observation_client"]:currentTarget.value})
    }
 
    const signer = () => {
        setError("")
        
        if(bi.client_id === 0 || bi.machine_id === 0 || bi.travail_a_faire === "" || bi.constatation_arrivee === ""
        ||  bi.travaux_realise === ""){
            setError("Veuillez remplir tout les champs")
        }
        else if (bi.numero_bl === "" && fourniture.length !== 0){
            setError("veuillez entrer le numero de bl")
        }
        else{
            var fournitureToSend
            var forfaitToSend
            var forfaitToSend = []

            if(fourniture.length != 0){
                fournitureToSend = fourniture
            }else{
                fournitureToSend = []
            }
            
            if(bi["prix_total_forfait"] != 0){
               if (forfaitLundi.date != ""){
                    forfaitToSend.push(forfaitLundi)
               }
               if (forfaitMardi.date != ""){
                    forfaitToSend.push(forfaitMardi)
                }
                if (forfaitMercredi.date != ""){
                    forfaitToSend.push(forfaitMercredi)
               }
               if (forfaitJeudi.date != ""){
                forfaitToSend.push(forfaitJeudi)
                }
                if (forfaitVendredi.date != ""){
                    forfaitToSend.push(forfaitVendredi)
               }
            }
            var photoToSend = []
            if(photos.photo1.name){
                photoToSend.push({"path": photos.photo1.base64,"commentaire":photos.photo1.name})
            }
            if(photos.photo2.name){
                photoToSend.push({"path": photos.photo2.base64,"commentaire":photos.photo2.name})
            }
            if(photos.photo3.name){
                photoToSend.push({"path": photos.photo3.base64,"commentaire":photos.photo3.name})
            }
      
            
                var biToSend = {
                    "numero_bi":bi.numero_bi,
                    "client_id":bi.client_id,
                    "client":client,
                    "machine":machine,
                    "machine_id":bi.machine_id,
                    "travail_a_faire":bi.travail_a_faire,
                    "numero_bl" : bi.numero_bl,
                    "prix_total_fourniture" : bi.prix_total_fourniture,
                    "prix_total_forfait" : bi.prix_total_forfait,
                    "constatation_arrivee" : bi.constatation_arrivee,
                    "travaux_realise" : bi.travaux_realise,
                    "observation_client" : bi.observation_client,
                    "fourniture" : fournitureToSend,
                    "forfait" : forfaitToSend,
                    "pictures":photoToSend
                }
               
                history.push({pathname:"/signBi",state:{bi:biToSend}})
            }
        
    }

    const annul = () => {
        setError("")
        setBi({"client_id":"0",
        "machine_id":"0",
        "travail_a_faire":"",
        "numero_bl" : "",
        "prix_total_fourniture" : 0,
        'fourniture' : [],
        "prix_total_forfait" : 0,
        "constatation_arrivee" : "",
        "travaux_realise" : "",
        "observation_client" : ""})

        setIsClient(true)
        setClientSelectId(0)
        setClient()
        setListMachine([])
        setOpenListMachine(true)
        setNombreFourniture([])
        setPhotos({
            ["photo1"]:{},
            ["photo2"]:{},
            ["photo3"]:{}
        })
        setForfaitLundi({
            "date" : "",
            "nombre_deplacement" : 0,
            "nombre_heure" : 0,
            "repas" : 0,
            "hotel" : 0
        })
        setForfaitMardi({
            "date" : "",
            "nombre_deplacement" : 0,
            "nombre_heure" : 0,
            "repas" : 0,
            "hotel" : 0
        })
        setForfaitMercredi({
            "date" : "",
            "nombre_deplacement" : 0,
            "nombre_heure" : 0,
            "repas" : 0,
            "hotel" : 0
        })
        setForfaitJeudi({
            "date" : "",
            "nombre_deplacement" : 0,
            "nombre_heure" : 0,
            "repas" : 0,
            "hotel" : 0
        })
        setForfaitVendredi({
            "date" : "",
            "nombre_deplacement" : 0,
            "nombre_heure" : 0,
            "repas" : 0,
            "hotel" : 0
        })
        setOpenPhoto(false)
    }

    const handleChangePhoto = async ({currentTarget}) => {
        
        var file = currentTarget.files[0]
        var type = file.type.split("/")
        var base64
        type = type[1]
        var name = bi.numero_bi +"_" +currentTarget.name+"."+type
        await getBase64(file).then(
            data => base64 = data
          );
        setPhotos({...photos,[currentTarget.name]:{
            "name" : name,
            "base64":base64
        }})
    }

    function getBase64(file) {
        return new Promise((resolve, reject) => {
          const reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = () => resolve(reader.result);
          reader.onerror = error => reject(error);
        });
      }


    return loading ? (<Loading title={"chargement"} />) : (
        <>
            <div className="containerBi">
                <Form className="FormAjoutBi">
                    
                    <div className='containerAjoutOnBi'>
                    <div className="BiSelectClientMachine"><p className='fontBold fontTitle'>Ajout d'un bon d'intervention</p></div>
                    <div className="BiSelectClientMachine">
                        {listClient ? 
                        (
                            
                            <Form.Group className="  mb-3" controlId="client">
                                <span className="fontBold">Client : </span>
                                <select value={bi.client_id} className="selectClient" onChange={handleChangeSelectClient} >
                                    <option key='0' value='0'>client</option>
                                    {listClient.map((client) => (
                                    <option key={client.client_id} value={client.client_id}>{client.societe}</option>))}
                                </select>
                            </Form.Group>
                        ) : "" }

                        <Form.Group className="mb-3" controlId="machine">
                        <span className="fontBold">Machine : </span>
                            <select value={bi.machine_id} className="selectClient" onChange={handleChangeSelectMachine} disabled={openListMachine}>
                                <option key='0' value='0'>Machine</option>
                                {listMachine.map((machine) => (
                                <option key={machine.machine_id} value={machine.machine_id}>{machine.nom}</option>))}
                            </select>
                        </Form.Group>
                    </div>
                    <div className="travailAFaire">
                        <FloatingLabel controlId="travail_a_faire" label="Travaux à réaliser" className="mb-3">
                            <Form.Control as="textarea" className="textAreaTravail" placeholder="Travaux à réaliser" value={bi.travail_a_faire} onChange={handleChangeTravauxARealiser}/>
                        </FloatingLabel>
                    </div>
                    <div className="containerNombreFourniture">
                        <Form.Group className="mb-3" controlId="numero_bl">
                            <Form.Label  className="mb-3 fontBold">Numéro Bon de livraison</Form.Label>
                            <Form.Control type="text" placeholder="Numéro Bon de livraison" value={bi.numero_bl} name="numero_bl" onChange={handleChange}/>
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="nombreFourniture">
                            <Form.Label  className="mb-3 fontBold">Nombre de fourniture</Form.Label>
                            <Form.Control type="number" min="0" placeholder="Nombre de fourniture" value={nombreFourniture.length} name="nombreFourniture" onChange={handleChangeNombreFourniture}/>
                        </Form.Group>
                    </div>
                    <div className="containerTableFourniture">
                            <Table className="tableFourniture">
                                <thead className="theadTableFourniture">
                                    <tr>
                                        <th>Piece</th>
                                        <th>Quantité</th>
                                        <th>Prix Unitaire</th>
                                        <th>Prix Total</th>
                                    </tr>
                                </thead>
                                <tbody style={{textAlign:'center'}}>
                                    {nombreFourniture.map((i)=><FournitureRow key={i} ptot={ptot} index={i} changeFourniture={changeFourniture}/>)}
                                </tbody>
                            </Table>
                    </div>
                    <div className="containerPrixTotalFourniture">
                        <Form.Group className="mb-3" controlId="ptFourniture">
                            <Form.Label  className="mb-3">Prix total fourniture</Form.Label>
                            <Form.Control type="text" disabled placeholder="Prix total fourniture" value={bi.prix_total_fourniture} name="ptFourniture"/>
                        </Form.Group>
                    </div>
                    <div className="containerTableFourniture">
                            <Table className="tableFourniture">
                                <thead className="theadTableFourniture">
                                    <tr>
                                        <th>Jour</th>
                                        <th>date</th>
                                        <th>Nombre de Heure de déplacement</th>
                                        <th>Nombre d'heure</th>
                                        <th>Nombre de nuit d'hotel</th>
                                        <th>Nombre de repas</th>
                                    </tr>
                                </thead>
                                <tbody style={{textAlign:'center'}}>
                                    <tr>
                                        <th>Lundi</th>
                                        <th><Form.Control type="date" disabled={isClient} value={forfaitLundi.date} name="date" onChange={handleChangeForfaitLundi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitLundi.nombre_deplacement} name="nombre_deplacement" onChange={handleChangeForfaitLundi} onBlur={onBlurForfaitLundi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitLundi.nombre_heure} name="nombre_heure" onChange={handleChangeForfaitLundi} onBlur={onBlurForfaitLundi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitLundi.hotel} name="hotel" onChange={handleChangeForfaitLundi} onBlur={onBlurForfaitLundi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitLundi.repas} name="repas" onChange={handleChangeForfaitLundi} onBlur={onBlurForfaitLundi}/></th>
                                    </tr>
                                    <tr>
                                        <th>Mardi</th>
                                        <th><Form.Control type="date" disabled={isClient} value={forfaitMardi.date} name="date" onChange={handleChangeForfaitMardi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMardi.nombre_deplacement} name="nombre_deplacement" onChange={handleChangeForfaitMardi} onBlur={onBlurForfaitMardi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMardi.nombre_heure} name="nombre_heure" onChange={handleChangeForfaitMardi} onBlur={onBlurForfaitMardi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMardi.hotel} name="hotel" onChange={handleChangeForfaitMardi} onBlur={onBlurForfaitMardi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMardi.repas} name="repas" onChange={handleChangeForfaitMardi} onBlur={onBlurForfaitMardi}/></th>
                                    </tr>
                                    <tr>
                                        <th>Mercredi</th>
                                        <th><Form.Control type="date" disabled={isClient} value={forfaitMercredi.date} name="date" onChange={handleChangeForfaitMercredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMercredi.nombre_deplacement} name="nombre_deplacement" onChange={handleChangeForfaitMercredi} onBlur={onBlurForfaitMercredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMercredi.nombre_heure} name="nombre_heure" onChange={handleChangeForfaitMercredi} onBlur={onBlurForfaitMercredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMercredi.hotel} name="hotel" onChange={handleChangeForfaitMercredi} onBlur={onBlurForfaitMercredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitMercredi.repas} name="repas" onChange={handleChangeForfaitMercredi} onBlur={onBlurForfaitMercredi}/></th>
                                    </tr>
                                    <tr>
                                        <th>Jeudi</th>
                                        <th><Form.Control type="date" disabled={isClient} value={forfaitJeudi.date} name="date" onChange={handleChangeForfaitJeudi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitJeudi.nombre_deplacement} name="nombre_deplacement" onChange={handleChangeForfaitJeudi} onBlur={onBlurForfaitJeudi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitJeudi.nombre_heure} name="nombre_heure" onChange={handleChangeForfaitJeudi} onBlur={onBlurForfaitJeudi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitJeudi.hotel} name="hotel" onChange={handleChangeForfaitJeudi} onBlur={onBlurForfaitJeudi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitJeudi.repas} name="repas" onChange={handleChangeForfaitJeudi} onBlur={onBlurForfaitJeudi}/></th>
                                    </tr>
                                    <tr>
                                        <th>Vendredi</th>
                                        <th><Form.Control type="date" disabled={isClient} value={forfaitVendredi.date} name="date" onChange={handleChangeForfaitVendredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitVendredi.nombre_deplacement} name="nombre_deplacement" onChange={handleChangeForfaitVendredi} onBlur={onBlurForfaitVendredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitVendredi.nombre_heure} name="nombre_heure" onChange={handleChangeForfaitVendredi} onBlur={onBlurForfaitVendredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitVendredi.hotel} name="hotel" onChange={handleChangeForfaitVendredi} onBlur={onBlurForfaitVendredi}/></th>
                                        <th><Form.Control type="number" disabled={isClient} value={forfaitVendredi.repas} name="repas" onChange={handleChangeForfaitVendredi} onBlur={onBlurForfaitVendredi}/></th>
                                    </tr>
                                </tbody>
                            </Table>
                    </div>
                    <div className="containerPrixTotalFourniture">
                        <Form.Group className="mb-3" controlId="ptForfait">
                            <Form.Label  className="mb-3">Prix total Forfait</Form.Label>
                            <Form.Control type="text" disabled placeholder="Prix total forfait" value={bi.prix_total_forfait} name="ptForfait"/>
                        </Form.Group>
                    </div>
                    <div className="travailAFaire">
                        <FloatingLabel controlId="constatation_arrive" label="Constatation a l'arrivée" className="mb-3">
                            <Form.Control as="textarea" className="textAreaTravail" placeholder="Constatation a l'arrivée" value={bi.constatation_arrivee} onChange={handleChangeConstatation}/>
                        </FloatingLabel>
                    </div>
                    <div className="travailAFaire">
                        <FloatingLabel controlId="travaux_realise" label="Travaux effectivement réalisés" className="mb-3">
                            <Form.Control as="textarea" className="textAreaTravail" placeholder="Travaux effectivement réalisés" value={bi.travaux_realise} onChange={handleChangeTraveauRealise}/>
                        </FloatingLabel>
                    </div>
                    
                    <div className="travailAFaire">
                        <Button className="m-2" onClick={()=>setOpenPhoto(!openPhoto)}>Ajouter des photos</Button>
                    </div>
                    {openPhoto ? (<div className="containerPhotoBiAdd">
                    <div className="photos">
                        <Form.Group controlId="photo1" className="mb-3 containerInputPhoto">
                            <Form.Label>1ere photo</Form.Label>
                            <Form.Control type="file" name="photo1" accept='image/*' onChange={handleChangePhoto}/>
                        </Form.Group>
                        {photos.photo1.base64 ? <img src={photos.photo1.base64} className="photoBiAdd"/> : "" }
                        <Form.Group controlId="photo2" className="mb-3 containerInputPhoto">
                            <Form.Label>2eme photo</Form.Label>
                            <Form.Control type="file" name="photo2" accept='image/*' onChange={handleChangePhoto}/>
                        </Form.Group>
                        {photos.photo1.base64 ? <img src={photos.photo2.base64} /> : "" }
                        <Form.Group controlId="photo3" className="mb-3 containerInputPhoto">
                            <Form.Label>3eme photo</Form.Label>
                            <Form.Control type="file" name="photo3" accept='image/*' onChange={handleChangePhoto}/>
                        </Form.Group>
                        {photos.photo1.base64 ? <img src={photos.photo3.base64} /> : "" }
                    </div>
                    
                    </div>) : ""}
                    {error ? (<div className="containerRowFormClientError">
                    <p>{error}</p>
                    </div>) : ""}
                    <div className="travailAFaire">
                        <Button className="m-2" onClick={annul}>Annuler</Button>
                        <Button className="m-2" onClick={signer}>Signer</Button>
                    </div>
                    </div>
                </Form>
            </div>
        </>
        
    )
}

export default BiAdd