import React, { useEffect, useState } from 'react'
import { Table } from 'react-bootstrap'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import DocumentRow from '../../components/documents/DocumentRow'
import Loading from '../../components/Loading'
import { getAllDocument,delDoc } from '../../services/Documents'

const Documents = () =>{

    const [listDocument,setListDocument] = useState()
    const [loading,setLoading] = useState(false)
    const history = useHistory()
    useEffect(()=>{
        setLoading(true)
        getDocuments()
    },[])

    const getDocuments = async () => {
        let resp = await getAllDocument()
        if(resp === 0){
            setListDocument([])
        }else{
            setListDocument(resp)
        }
        
        setLoading(false)
    }

    const delDocument = async (id) => {
        setLoading(true)
        let resp = await delDoc(id)
        if(resp === 1){
            getDocuments()
        }else{
            setLoading(false)
        }
    }
    
    const modifDoc = (id) => {
        history.push("/document/modify/"+id)
        
    }
    return loading ? (<Loading title={"chargement"} />) : (
        
        <div className="containerMachine">
            <div className='containerTableDocument'>
            <div className="containerTable">
                    <Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Nom</th>
                                <th>Type</th>
                                <th>Mots clés</th>
                                <th>Créer le</th>
                                <th>Dernière modification</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {listDocument ? listDocument.map(document => <DocumentRow key={document.document_id} document={document} delDocument={delDocument} modifDoc={modifDoc}/>) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>
                </div>
        </div>
        </div>
    )
}

export default Documents