export async function getAllUsers(){
    return await fetch('https://www.emanorcloud.fr/api/v2/users', {
         method: 'GET',
         mode: "cors",
         headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
         .then(response => response.json())
 }


 export async function supprUser(id){
    return await fetch('https://www.emanorcloud.fr/api/v2/users/del/'+id, {
         method: 'DELETE',
         mode: "cors",
         headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
         .then(response => response.json())
 }

 export async function archiveUser(){
        return await fetch('https://www.emanorcloud.fr/api/v2/users/searchArchive', {
             method: 'GET',
             mode: "cors",
             headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
             .then(response => {
                     if(response.status === 204){
                             return 0
                     }else{
                             return response.json()
                     }
                })
     }

export async function registerUser(arrUser){
        return await fetch('https://www.emanorcloud.fr/api/v2/register', {
         method: 'POST',
         mode: "cors",
         body : JSON.stringify(arrUser),
         headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
         .then(response => {
                if(response.status === 422){
                        return 0
                }else{
                        return response.json()
                }
           })
}