import React, { useEffect, useState } from 'react' 
import Loading from '../../components/Loading'
import VisualisationClient from '../../components/clients/VisualisationClients'
import AjoutClient from '../../components/clients/AjoutClient'
import '../../css/Clients.css'


const ContainerClient = ({match,test}) => {
    const [page,setPage] = useState()
    const [loading,setLoading] = useState(true)
    const [clientModify,setClientModify] = useState(
        {
            "societe" : "",
            "interlocuteur":"",
            "numero_tel":"",
            "adresse_mail":"",
            "adresse":"",
            "ville":"",
            "code_postal":"",
            "forfait_dep":50,
            "forfait_heure":65,
            "hotel":120,
            "repas":25,
            "new_client":"",
            "created_at":"",
            "updated_at":""
        }
    )

    const modifClient = (client) => {
        setClientModify(client)
        setPage('/client/modify')
    }

   

    const switchPage = (page) => {
        switch(page){
            case '/client/add' : 
                return <AjoutClient clientModify={clientModify} crea={true}/>
            case '/client/modify' : 
                return <AjoutClient clientModify={clientModify} crea={false}/>
            case '/client/archive' : 
                return <VisualisationClient param={'archive'} />
            default :
                return <VisualisationClient  param={'visualisation'} modifClient={modifClient} />
        }
           
    }

    
    
    useEffect(()=>{
     
        setPage(match.path)
        setLoading(false)
    },[match])

    return loading ? (<Loading title={"Chargement"} />) 
        : ( <div className="containerClient">{switchPage(page)}</div>)
}

export default ContainerClient