import React,{useState,useEffect} from 'react'
import { getAllUsers, supprUser } from '../../services/Utilisateurs'
import { Table, Form, Button } from 'react-bootstrap'
import '../../css/Utilisateurs.css'
import UtilisateurRow from '../../components/UtilisateurRow'
import Loading from '../../components/Loading'
import Popup from 'reactjs-popup'
import { getItem } from '../../services/localStorage'
import UtilisateurVisu from '../../components/UtilisateurVisu'
import FormUtilisateur from '../../components/FormUtilisateur'
import { useHistory } from 'react-router-dom'

const ContainerUtilisateurs = () =>{
    const history = useHistory()
    const [utilisateurs,setUtilisateurs] = useState()
    const [loading, setLoading] = useState(true)
    const [openFormUtilisateur, setOpenFormUtilisateur] = useState(false)
    const [utilisateurModif,setUtilisateurModif] = useState()
    useEffect(()=>{
        async function getUtilisateurs(){
            var resp = await getAllUsers()
            setUtilisateurs(resp)
            setLoading(false)
        }
        if(history.location.pathname === "/utilisateur/add"){
            setLoading(false)
            setOpenFormUtilisateur(true)
           }else{
            getUtilisateurs()
           }
    },[])
    
    const setModif = (user) => {
        setUtilisateurModif(user)
        setOpenFormUtilisateur(true)
    }

    const getUsers = async () => {
        
        setLoading(true)
        var resp = await getAllUsers()
        setUtilisateurs(resp)
        setLoading(false)
    }

    const retourPage = () => {
        
        getUsers()
        setOpenFormUtilisateur(false)
    }

    return loading ? (<Loading title={"Chargement"}/>) : (
        (!openFormUtilisateur ? <><UtilisateurVisu users={utilisateurs} setModif={setModif} /></> 
            : 
            <>
                <FormUtilisateur user={utilisateurModif} retourPage={retourPage}/>
            </>
        )
    )
}

export default ContainerUtilisateurs