export async function updateMachine(arrMachine){
        return await fetch('https://www.emanorcloud.fr/api/v2/machines', {
                 method: 'PUT',
                 mode: "cors",
                 body : JSON.stringify(arrMachine),
                 headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
                 .then(response => {
                         if(response.status === 204){
                                 return 0
                         }else{
                                 return response.json()
                         }
                    })
    }


