import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import { Button } from 'react-bootstrap'
import { getOneClient } from '../../services/Clients'
import { getItem } from '../../services/localStorage'
import { getAllArchiveMachines } from '../../services/Machines'
import PopUpVisuClient from '../clients/PopUpVisuClient'
import Loading from '../Loading'
import PopUpVisuMachine from './PopUpVisuMachine'


const MachineRow = ({machine,archiveMachine,desarchiveMachine,modifMachine}) => {
    const [client,setClient] = useState()
    const [openModClient, setOpenModClient] = useState(false)
    const [openModMachine, setOpenModMachine] = useState(false)
    const [loading,setLoading] = useState(false)
    const openModalVisuClient = () => {
        setOpenModClient(true)
    }
    const closeModalVisuClient = () => {
        setOpenModClient(false)
    }
    const archMachine = async (id) => {
        archiveMachine(id)
    }
    const closeModalVisuMachine = () => {
        setOpenModMachine(false)
    }
    const openModalVisuMachine = () => {
        setOpenModMachine(true)
    }

    useEffect(()=>{
        const getClientForMachine = async () =>{
            let resp = await getOneClient(machine.client_id)
            setClient(resp)
            setLoading(false)
        }
        getClientForMachine()
    },[machine])
    return loading ? (<Loading title={"chargement"} />) : (
        <>
        <tr className="ligneTableau">
        <td>{machine.nom}</td>
        <td>{machine.constructeur}</td>
        <td>{machine.numero_serie}</td>
        <td>{machine.type}</td>
        <td>{client ? (<>{client[0].societe}  <Button className="m-2 btnVisu" onClick={openModalVisuClient}><i className="tiny material-icons">remove_red_eye</i></Button></>) : null}</td>
        <td>{machine.created_at}</td>
        <td>{machine.updated_at}</td>
        {machine.deleted_at ? <td>{machine.deleted_at}</td> : null}
        <td>
            <Button className="m-2 btnVisu" onClick={openModalVisuMachine}><i className="tiny material-icons">remove_red_eye</i></Button>
            
        </td>
        {client ? <PopUpVisuClient open={openModClient} closeModalVisuClient={closeModalVisuClient}  client={client[0]}/> : null}
        <PopUpVisuMachine open={openModMachine} closeModalVisuMachine={closeModalVisuMachine}  machine={machine}/>
        </tr>
        
        </>
    )
}
export default MachineRow