import React,{useState} from "react";
import { Button } from "react-bootstrap";
import { Redirect } from "react-router-dom";
import Popup from "reactjs-popup";
import ContainerFormUtilisateur from "./FormUtilisateur";
import { getRole, updateUser } from "../services/Users";
import Signature from "./Signature";
import "../css/PopUpVisuUser.css"

const UtilisateurRow = ({user, suppressionUser,desarchiveUser,openModifUtilisateur}) => {
    const [open, setOpen] = useState(false);
    const [openSuppr,setOpenSuppr] = useState(false)
    const [openDesarchive, setOpenDesarchive] = useState(false)
    const [formModif, setFormModif] = useState(false)
    const closeModal = () => {
        setOpen(false) 
    };
    
    const openModal = (e) => {
        e.preventDefault()
        setOpen(true)
    }

    const openModalSuppr = (e) => {
        e.preventDefault()
        setOpenSuppr(true)
    }

    const closeModalSuppr = (e) => {
        setOpenSuppr(false)
    }
    const supprUtilisateur = async (id) => {
        suppressionUser(id)
        setOpenSuppr(false)
    }
    const openModalDesarchive = (e) => {
        e.preventDefault()
        setOpenDesarchive(true)
    }
    const closeModalDesarchive = (e) => {
        setOpenDesarchive(false)
    }
    const desarchiveUtilisateur = async(id) => {
        desarchiveUser(id)
        setOpenDesarchive(false)
    }
    

    return (
        <>
            <tr className="ligneTableau">
                <td>{user.lastname}</td>
                <td>{user.firstname}</td>
                <td>{user.login}</td>
                <td>{getRole(""+user.lvlUser_id)}</td>
                <td>{user.created_at}</td>
                <td>{user.updated_at}</td>
                {user.deleted_at ? <td>{user.deleted_at}</td> : null}
                <td>
                    <Button className="m-2 btnVisu" onClick={openModal}><i className="tiny material-icons">remove_red_eye</i></Button>
                    {!user.deleted_at ? <Button className="m-2 btnEdit" onClick={() => {openModifUtilisateur(user)}}><i className="material-icons">edit</i></Button> : ""}
                    {!user.deleted_at ? <Button className="m-2 btnDel" onClick={openModalSuppr}><i className="material-icons">delete</i></Button> : 
                        <Button className="m-2 btnDel" onClick={openModalDesarchive}><i className="material-icons">open_in_browser</i></Button> }
                   
                </td>
            </tr>

            <Popup open={open} closeOnDocumentClick onClose={closeModal}>
                {console.log(user)}
                <div className="containerPopUpVisuUser">
                    <p className='utilisateur'>Utilisateur</p>                   
                    <div className="containerUtilisateurPopUp">
                        <div className='utilisateurNom'><p><span className='nomBold'>Nom de famille :</span> {user.lastname}</p>
                        <p><span className='nomBold'>Prénom :</span> {user.firstname}</p></div>
                        <div className="utilisateurNom"><p><span className='nomBold'>Niveau :</span> {getRole(""+user.lvlUser_id)}</p>
                        <p><span className='nomBold'>Login :</span> {user.login}</p></div>                       
                    </div>
                    <div className="signaturePopup">
                        {user.signature === "path" ? <><span className="fontBold">Signature :</span><div className="aucuneSignature mb-4"><span>aucune signature enregistrée</span></div></> : <Signature user={user} edit={false}/> }
                    </div>
                    <div className="dataUtilisateurPopUp">{user.data.map((d) =>  <p key={d.userData_id}>{d.key} : {d.value}</p>)}</div>
                    <div className="created"><p>Créer le : {user.created_at}</p><p>Modifier le : {user.updated_at}</p></div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModal}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            <Popup open={openSuppr} closeOnDocumentClick onClose={closeModalSuppr}>
                <div className="containerValidArchiveUtilisateur">
                    <p>Suppression</p>                   
                        <p>Voulez vous vraiment supprimer l'utilisateur {user.lastname} {user.firstname}</p>
                    <div className="containerBouttonPopUp">
                        <Button onClick={()=>{supprUtilisateur(user.user_id)}}>Valider</Button>
                        <Button onClick={closeModalSuppr}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            <Popup open={openDesarchive} closeOnDocumentClick onClose={closeModalDesarchive}>
                <div className="containerValidArchiveUtilisateur">
                    <p>Desarchivage</p>                   
                        <p>Voulez vous vraiment desarchiver l'utilisateur {user.lastname} {user.firstname}</p>
                    <div className="containerBouttonPopUp">
                        <Button onClick={()=>{desarchiveUtilisateur(user.user_id)}}>Valider</Button>
                        <Button onClick={closeModalDesarchive}>Fermer</Button>
                    </div>
                </div>
            </Popup>
        </>
    )
}

export default UtilisateurRow