import { getItem } from "./localStorage"

export async function getUserLog(){
   return await fetch('https://www.emanorcloud.fr/api/v2/users/search/'+getItem("userId"), {
        method: 'GET',
        mode: "cors",
        headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
        .then(response => response.json())
}

export function getRole(id){
        switch(id){
                case '1':
                        return 'technicien'
                case '2':
                        return 'Responsable Technique'
                case '3':
                        return 'Directeur'
                case '4':
                        return 'Admin Dev'
                default : 
                        return 'technicien'
        }  
}

export async function getUserById(id){
        return await fetch('https://www.emanorcloud.fr/api/v2/users/search/'+id, {
             method: 'GET',
             mode: "cors",
             headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
             .then(response => response.json())
     }

export async function updateUser(arrUser){
        return await fetch("https://www.emanorcloud.fr/api/v2/users/update", {
                mode: "cors",
                method: 'PUT',
                body : JSON.stringify(arrUser),
                headers: {'Content-Type': 'application/json','Authorization': 'bearer ' + localStorage.getItem('tokenUser')}
            }).then(response =>  response.json());
}

export async function updatePassword(arrPass){
        return await fetch("https://www.emanorcloud.fr/api/v2/users/pwd", {
                mode: "cors",
                method: 'PUT',
                body : JSON.stringify(arrPass),
                headers: {'Content-Type': 'application/json','Authorization': 'bearer ' + localStorage.getItem('tokenUser')}
            }).then(response =>  response.json());
}