import React, { useState } from 'react' 
import { useEffect } from 'react'
import Loading from '../Loading'
import { archiveClient, getAllClients,getArchiveClients, updateClient } from '../../services/Clients'
import { Table } from 'react-bootstrap'
import '../../css/Clients.css'
import ClientRow from './ClientRow'
import { getNow } from '../../assets/helpers/Date'

const VisualisationClient = ({param,modifClient}) => {
    
    const [clients,setClients] = useState()
    const [loading,setLoading] = useState(false)
    const [message,setMessage] = useState("") 

    const modifyClient = (clientModif) =>{
        modifClient(clientModif)
    }

    useEffect(()=>{
        setLoading(true)
        
        if(param ==="archive"){
            getClientsArchive()
        }else{
            getClients()
        }
    },[])

    const getClients = async () => {
        let resp = await getAllClients()
        if (resp != 0){
            console.log(resp)
            setClients(resp)
            setLoading(false)
        }else{
            setMessage("Aucun client a afficher")
            setLoading(false)
        }
    }
    
    const getClientsArchive = async () => {
            
        let resp = await getArchiveClients()
        console.log(resp)
        if (resp != 0){
            console.log(resp)
            setClients(resp)
            setLoading(false)
        }else{
            setMessage("Aucun client archivé")
            setLoading(false)
        }
    }

    const delClient = async (id) => {
        let resp = await archiveClient(id)
        if(resp ==1){
            getClients()
        }
    }

    const desarchClient = async (id) => {
        let arrDesarchive = {
            "client_id":id,
            "updated_at":getNow(),
            "deleted_at":null
        }

        let resp = await updateClient(arrDesarchive)
        console.log(resp)
        if(resp == 1){
            getClientsArchive()
        }
    }
    
    return loading ? (<Loading title={"Chargement"} />) : (
        <div className='containerTableUtilisateurs'>
            {message !== "" ? (<div className="containerArchiveClient"><p className='fontBold fontTitle'>{message}</p> </div>)
            : (
                <div className="containerTable">
                    <Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Société</th>
                                <th>Interlocuteur</th>
                                <th>Numéro de téléphone</th>
                                <th>Adresse mail</th>
                                <th>Forfait</th>
                                <th>Créer le</th>
                                <th>Dernière modification</th>
                                {param === "archive" ? <th>Archivé le</th> : null}
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {clients ? clients.map(client => <ClientRow key={client.client_id} client={client} modifyClient={modifyClient} delClient={delClient} desarchClient={desarchClient} />) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>
                </div>
            )}
        </div>
    )
}

export default VisualisationClient