import { Button } from 'react-bootstrap'
import React,{useState,useEffect} from 'react'
import { Form } from 'react-bootstrap'
import Loading from '../../components/Loading'
import { getAllBi } from '../../services/Bi'
import { postPicture } from '../../services/Picture'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'

const BiAddPicture = () =>{
    const [listBi,setListBi] = useState()
    const [loading,setLoading] = useState(false)
    const [biSelected,setBiSelected] = useState(0)
    const [numberBi,setNumberBi] = useState(0)
    const [listPhoto,setListPhoto] = useState([])
    const [listNewPhoto,setListNewPhoto] = useState([])
    const [nbNewPhoto,setNbNewPhoto] = useState(0)
    const [nbOldPhoto,setNbOldPhoto] = useState(0)
    const [isPlus,setIsPlus] = useState(true)
    const [refresh,setRefresh] = useState(0)
    const [error,setError] = useState()
    const history = useHistory()
    useEffect(()=>{
        setLoading(true)
        allBi()
    },[])

    useEffect(()=>{
        if(biSelected !== 0){
            getPicture(biSelected)
            setListNewPhoto([])
        }
    },[biSelected])

    const getPicture = (id) => {
        
        listBi.map((bi)=>{
            if(""+bi.bi_id === id){
                setListPhoto(bi.pictures)
                setNbOldPhoto(bi.pictures.length)
                setNumberBi(bi.numero_bi)
                setIsPlus(false)
            }
        })
    } 

    const allBi = async () => {
        let resp = await getAllBi()
        setListBi(resp)
        setLoading(false)
    }

    const handleChangeBiSelected = (e) => {
        setBiSelected(e.target.value)
    }

    const ajoutNewPhoto = () => {
        listNewPhoto.push({
            'number' : listNewPhoto.length + nbOldPhoto + 1,
            'path':'',
            'commentaire':''
        })
        setNbNewPhoto(listNewPhoto.length)
        setRefresh(refresh+1)
    }

    const handleChangePhoto = async ({currentTarget}) => {
        var file = currentTarget.files[0]
        var type = file.type.split("/")
        var base64
        type = type[1]
        var name = numberBi +"_" +currentTarget.name+"."+type
       
        await getBase64(file).then(
            data => base64 = data
          );
        var pos = currentTarget.name.split("photo")
        pos = pos[1]
        pos = pos - 1 - nbOldPhoto
        listNewPhoto[pos].path = base64
        listNewPhoto[pos].commentaire = name
    }

    function getBase64(file) {
        return new Promise((resolve, reject) => {
          const reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = () => resolve(reader.result);
          reader.onerror = error => reject(error);
        });
      }

      const annul = () => {
        setBiSelected(0)
        setListNewPhoto([])
        setListPhoto([])
        setRefresh(0)
        setIsPlus(true)
      }

      const sendNewPhoto = async () => {
          setError('')
            var retour = []
            var photoToSend = []
            listNewPhoto.map(async (photo)=>{
                photoToSend = {
                    'path':photo.path,
                    'commentaire':photo.commentaire,
                    'bi_id':biSelected
                }
                let resp = await postPicture(photoToSend)
               
                if (resp != true){
                    retour.push(photo.commentaire)
                }
            })
            if(retour.length != 0){
                setError("Erreur lors de l'insertion d'une ou plusieur photo")
                annul()
            }else{
               history.push("/bis")
            }
      }

    return loading ?(<Loading title={'chargement'} />) : (
        <div className="containerMachine">
        <div className="containerBiPt">
            
            <Form  className="FormAjoutBi">
                <div><p className='fontBold mt-3'>Selectionner un Bon d'intervention</p></div>
            {listBi ? 
            (
                <Form.Group className="  mb-3" controlId="client">
                    <select value={biSelected} className="selectBi" onChange={handleChangeBiSelected} >
                        <option key='0' value='0'>Bi</option>
                        {listBi.map((bi) => (
                        <option key={bi.bi_id} value={bi.bi_id}>{bi.numero_bi}</option>))}
                    </select>
                </Form.Group>
            ) : "" }
            {listPhoto.length != 0 ?(
                <div className='photoBiLie'>
                    <div className='fontBold mb-3'>Photos déjà liées au Bi : </div>
                    {listPhoto.map((photo)=>(
                        <img key={photo.picture_id} src={photo.path} alt={photo.commentaire}/>
                    ))}
                </div>
            ) : <div>Il n'y a pas de photo pour ce Bi</div>}
            <div className="newPhoto">
                <div><p className='fontBold'>Ajouter des photos : </p></div>
                <Button onClick={ajoutNewPhoto} disabled={isPlus}>+</Button>
                {listNewPhoto.length !== 0 ? (
                    <>
                        {listNewPhoto.map((d)=>(
                            
                            <div key={d.number} className='containerNewPhoto'>
                               
                                <Form.Group controlId={"photo"+d.number} className="mb-3 inputPhoto">
                                    <Form.Label className='fontBold'>Photo : </Form.Label>
                                    <Form.Control type="file" name={"photo"+d.number} accept='image/*' data={d.number} onChange={handleChangePhoto}/>
                                </Form.Group>
                            </div>
                        ))}
                    </>
                ) : ""}
                
            </div>
            <div className='mb-5 containerButtonAddNewPhoto'>
                <Button onClick={sendNewPhoto}>Envoyer</Button>
                <Button onClick={annul}>Annuler</Button>
            </div>
            </Form>
        </div>
        </div>
    )
}

export default BiAddPicture