export async function postPicture(arrPitcure){
    return await fetch('https://www.emanorcloud.fr/api/v2/pictures', {
             method: 'POST',
             mode: "cors",
             body : JSON.stringify(arrPitcure),
             headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
             .then(response => {
                     if(response.status === 204){
                             return 0
                     }else{
                             return response.json()
                     }
                })
}


export async function deletePicture(id){
    return await fetch('https://www.emanorcloud.fr/api/v2/pictures/'+id, {
             method: 'DELETE',
             mode: "cors",
             headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
             .then(response => {
                     if(response.status === 204){
                             return 0
                     }else{
                             return 1
                     }
                })
}