import React from 'react' 
import { useEffect } from 'react'
import { useState } from 'react'
import { Button, Form } from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import Popup from 'reactjs-popup'
import { getNow } from '../../assets/helpers/Date'
import { isMail, isNumb } from '../../assets/helpers/general'
import { postClient, updateClient } from '../../services/Clients'
import Loading from '../Loading'
import PopUpForfaitClient from './PopUpForfaitClient'
import '../../css/FormAjoutClient.css'

const AjoutClient = ({clientModify,crea}) => {
    const [client,setClient] = useState(clientModify)
    const [loading,setLoading] = useState(false)
    const [error,setError] = useState()
    const [editForfait,setEditForfait] = useState(true)
    const [openModForfait,setOpenModForfait] = useState(false)
    const [creaForfait,setCreaForfait] = useState(true)
    const history = useHistory()
    const [openModalValid,setOpenModenValid] = useState(false)

    const handleChange = ({currentTarget}) => {
        setClient({...client,[currentTarget.name]:currentTarget.value})
    }

    const sendClient = async () => {
        setError("")
        if(client.societe === "" || client.interlocuteur === "" ||client.numero_tel === "" ||
        client.adresse_mail === "" || client.adresse === "" || client.ville === "" ||
        client.code_postal === ""){
            setError("Tout les champs doivent être remplit")
        }else if(!isMail(client.adresse_mail)){
                setError("Adresse mail non valide")
        }else if(!isNumb(client.numero_tel)){
            setError("Numero de téléphone non valide")
        }else{
           if(crea === false){
                sendUpdateClient()
           }else{
                createClient()
           }
        }
    }

    const createClient = async () => {
        var clientToSend = {
            "adresse":client.adresse,
            "adresse_mail":client.adresse_mail,
            "code_postal":client.code_postal,
            "forfait_dep":client.forfait_dep,
            "forfait_heure":client.forfait_heure,
            "hotel":client.hotel,
            "interlocuteur":client.interlocuteur,
            "numero_tel":client.numero_tel,
            "repas":client.repas,
            "societe":client.societe,
            "ville":client.ville,
            "new_client":"0",
            "created_at": getNow(),
            "updated_at": getNow(),
        }
        console.log(clientToSend)
        let resp = await postClient(clientToSend)
        if(resp != 1){
            setError("erreur lors de la modification du client")
        }else{
            setOpenModenValid(true)
        }
    }
    
    const sendUpdateClient = async () => {
        
        var clientToSend = {
            "client_id":client.client_id,
            "adresse":client.adresse,
            "adresse_mail":client.adresse_mail,
            "code_postal":client.code_postal,
            "forfait_dep":client.forfait_dep,
            "forfait_heure":client.forfait_heure,
            "hotel":client.hotel,
            "interlocuteur":client.interlocuteur,
            "numero_tel":client.numero_tel,
            "repas":client.repas,
            "societe":client.societe,
            "ville":client.ville,
            "new_client":"0",
            "updated_at": getNow()
        }
        console.log(clientToSend)
        let resp = await updateClient(clientToSend)
        if(resp == 0){
            setError("Aucune modification a effectuée")
        }else{
            setOpenModenValid(true)
        }
    }

    const openModalForfait = () => {
        setOpenModForfait(true)
    }

    const closeModalForfaitClient = () => {
        setOpenModForfait(false)
    }

    const delForfait = () => {
        setClient(
            {
                "client_id" : client.client_id,
                "societe" : client.societe,
                "interlocuteur":client.interlocuteur,
                "numero_tel":client.numero_tel,
                "adresse_mail":client.adresse_mail,
                "adresse":client.adresse,
                "ville":client.ville,
                "code_postal":client.code_postal,
                "forfait_dep":"",
                "forfait_heure":"",
                "hotel":"",
                "repas":"",
                "new_client":"0",
                "created_at":client.created_at,
                "updated_at":client.updated_at
            
        })

    }

    const handleAnnul = () => {
        if (crea === false){
            setClient({
            "cleint_id" : "",
            "societe" : "",
            "interlocuteur":"",
            "numero_tel":"",
            "adresse_mail":"",
            "adresse":"",
            "ville":"",
            "code_postal":"",
            "forfait_dep":"",
            "forfait_heure":"",
            "hotel":"",
            "repas":"",
            "new_client":"0",
            "created_at":"",
            "updated_at":""})
            history.push("/clients")
        }else(
            setClient({"societe" : "",
            "interlocuteur":"",
            "numero_tel":"",
            "adresse_mail":"",
            "adresse":"",
            "ville":"",
            "code_postal":"",
            "forfait_dep":"",
            "forfait_heure":"",
            "hotel":"",
            "repas":"",
            "new_client":"0",
            "created_at":"",
            "updated_at":""})
        )
    }

    const closeModalValid = () => {
        
        setOpenModenValid(false)
        history.push("/clients")        
        
    }

    return loading ? (<Loading title={"Envoi"} />) : (
        <>
            <Form className="formAjoutClient">
                <div className="containerRowFormClient">
                    <div className="columnFormClient">
                        <p className='fontNameFormClient'>{crea ? "Création " : "Modification " }Client</p>
                    </div>
                </div>
                <div className="containerRowFormClient">
                    <div className="columnFormClient">
                        <Form.Group className="mb-3 formClientInput" controlId="societe">
                            <Form.Label  className="mb-3 fontNameFormClient">Societe</Form.Label>
                            <Form.Control type="text" placeholder="Societe" value={client ? client.societe : ""} name="societe" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                    <div className="columnFormClient">
                        <Form.Group className="mb-3" controlId="interlocuteur">
                            <Form.Label  className="mb-3 fontNameFormClient">Interlocuteur</Form.Label>
                            <Form.Control type="text" placeholder="interlocuteur" value={client ? client.interlocuteur : ""} name="interlocuteur" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                </div>
                <div className="containerRowFormClient">
                    <div className="columnFormClient">
                    <Form.Group className="mb-3" controlId="numero_tel">
                        <Form.Label  className="mb-3 fontNameFormClient">Numero de téléphone</Form.Label>
                        <Form.Control type="text" placeholder="Numero de téléphone" value={client ? client.numero_tel : ""} name="numero_tel" onChange={handleChange}/>
                    </Form.Group>
                    </div>
                    <div className="columnFormClient">
                        <Form.Group className="mb-3" controlId="adresse_mail">
                            <Form.Label  className="mb-3 fontNameFormClient">Adresse mail</Form.Label>
                            <Form.Control type="text" placeholder="Adresse mail" value={client ? client.adresse_mail : ""} name="adresse_mail" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                </div>
                <div className="containerRowFormClient">
                    <div className="columnFormClient">
                        <Form.Group className="mb-3" controlId="adresse">
                            <Form.Label  className="mb-3 fontNameFormClient">Adresse</Form.Label>
                            <Form.Control type="text" placeholder="Adresse" value={client ? client.adresse : ""} name="adresse" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                    <div className="columnFormClient">
                        <Form.Group className="mb-3" controlId="ville">
                            <Form.Label  className="mb-3 fontNameFormClient">Ville</Form.Label>
                            <Form.Control type="text" placeholder="Ville" value={client ? client.ville : ""} name="ville" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                    <div className="columnFormClient">
                        <Form.Group className="mb-3" controlId="code_postal">
                            <Form.Label  className="mb-3 fontNameFormClient">Code postal</Form.Label>
                            <Form.Control type="text" placeholder="Code postal" value={client ? client.code_postal : ""} name="code_postal" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                </div>
                <div className="containerRowFormClientForfait">
                    {client.forfait_dep ? (<><div className="columnFormClientForfait">
                        {client.forfait_dep ? <span className="textForfait">forfait Déplacement : {client.forfait_dep}</span> : ""}
                        {client.forfait_heure ? <span className="textForfait">forfait Heure : {client.forfait_heure}</span> : ""}
                        {client.hotel ? <span className="textForfait">Hotel : {client.hotel}</span> : ""}
                        {client.repas ? <span className="textForfait">Repas : {client.repas}</span> : ""}</div></>) : "" }
                    
                    <div className="columnFormClient">
                        <Button className='buttonEditFOrfait' onClick={openModalForfait}>{client.forfait_dep ? "Editer" : "Creer"} forfait</Button>
                        
                    </div>

                </div>
                {error ? (<div className="containerRowFormClientError">
                    <p>{error}</p>
                </div>) : ""}
                <div className="containerRowFormClientButton">
                    <div className="columnFormClientButton">
                        <Button onClick={handleAnnul}>Annuler</Button>
                        {crea ? <Button onClick={sendClient}>Créer</Button> : <Button onClick={sendClient}>Modifier</Button>}
                    </div>
                </div>
            </Form>
            <PopUpForfaitClient client={client} openForfaitClient={openModForfait} closeModalForfaitClient={closeModalForfaitClient} creation={creaForfait}/>
            <Popup open={openModalValid} closeOnDocumentClick onClose={closeModalValid}>
                <div className="containerClienPopUp">
                    <div className='containerMdp'>
                        <p >Client {crea ? "crée " : "modifié"} avec succès</p>
                    </div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalValid}>Fermer</Button>
                    </div>
                </div>
            </Popup>
        </>
    )
}

export default AjoutClient