import React,{useState,useEffect} from 'react'
import { useHistory } from 'react-router-dom'
import Loading from '../../components/Loading'
import AddMachine from '../../components/machines/AddMachine'
import ModifyMachine from '../../components/machines/ModifyMachine'
import VisualisationMachine from '../../components/machines/VisualisationMachine'
import '../../css/Machines.css'

const ContainerMachines = ({match}) =>{
    const [page,setPage] = useState()
    const [loading,setLoading] = useState(true)
    const [machineModify,setMachineModify] = useState()
    const history = useHistory()
    const modifMachine = (machine) => {
        setMachineModify(machine)
        history.push('/machine/modify')
    }

    const switchPage = (page) => {
        switch(page){
            case '/machine/add' : 
                return <AddMachine />
            case '/machine/modify' : 
                return <ModifyMachine machineModify={machineModify} />
            case '/machine/archive' : 
                return <VisualisationMachine param={'archive'} />
            default :
                return <VisualisationMachine  param={'visualisation'} modifMachine={modifMachine}/>
        }
           
    }

    useEffect(()=>{
        setPage(match.path)
        setLoading(false)
    },[match.path])
    
    
    return loading ? (<Loading title={"Chargement"} />) 
    : ( <div className="containerMachine">{switchPage(page)}</div>)
}

export default ContainerMachines