import React,{useState,useEffect} from 'react'
import { Button, Form } from 'react-bootstrap'
import { useHistory, useLocation } from 'react-router-dom/cjs/react-router-dom.min'
import Loading from '../../components/Loading'
import { getAllBi } from '../../services/Bi'
import { deletePicture } from '../../services/Picture'

const BiModifyPicture = () =>{
    const location = useLocation()
    const [listBi,setListBi] = useState([])
    const [loading,setLoading] = useState(false)
    const [biSelected,setBiSelected] = useState(0)
    const [listPhoto,setListPhoto] = useState([])
    const [isDel,setIsDel] = useState()
    const history = useHistory()
    useEffect(()=>{
        setLoading(true)
        allBi()
       
    },[])

    useEffect(()=>{
        
        if(biSelected !== 0){
            getPicture(biSelected)
        }
    },[biSelected])

    useEffect(()=>{
        getPicture(biSelected)
    },[isDel])

    

    const getPicture = (id) => {
        
        listBi.map((bi)=>{
            if(""+bi.bi_id === id){
                setListPhoto(bi.pictures)
            }
        })
    } 

    const allBi = async () => {
        let resp = await getAllBi()
        setListBi(resp)
        setLoading(false)
    }

    const handleChangeBiSelected = (e) => {
        setBiSelected(e.target.value)
    }

    const supprPhoto = async (id) =>{
        let resp = await deletePicture(id)
        
        if(resp != 1){

        }else{
            await allBi()
            setIsDel(1)
            if(listPhoto.length === 1){
                setListPhoto([])
            }
        }
    }

    return loading ?(<Loading title={'chargement'} />) : (
        <div className="containerMachine">
        <div className="containerBiPt">
            
            <Form className="FormAjoutBi">
            <div><p className='fontBold mt-3'>Selectionner un Bon d'intervention</p></div>
            {listBi ? 
            (
                <Form.Group className="  mb-3" controlId="client">
                    <select value={biSelected} className="selectClient" onChange={handleChangeBiSelected} >
                        <option key='0' value='0'>Bi</option>
                        {listBi.map((bi) => (
                        <option key={bi.bi_id} value={bi.bi_id}>{bi.numero_bi}</option>))}
                    </select>
                </Form.Group>
            ) : "" }
            {listPhoto.length != 0 ?(
                <div className='photoBiLie'>
                    <div>Photos déjà liées au Bi : </div>
                    {listPhoto.map((photo)=>(
                        <div key={photo.picture_id}>
                            <img  src={photo.path} alt={photo.commentaire}/>
                            <Button onClick={()=>(supprPhoto(photo.picture_id))}>Supprimer</Button>
                        </div>
                    ))}
                </div>
            ) : <div><p className='fontBold mb-3'>Il n'y a pas de photo pour ce Bi</p></div>}
            
            </Form>
        </div>
        </div>
    )
}

export default BiModifyPicture