import React,{useState,useEffect} from "react";


const FournitureRow = ({ptot,index,changeFourniture}) => {
    const [fourniture,setFourniture] = useState({
        "index":index,
        "nom":"",
        "quantite":0,
        "prix_unitaire":0
    })
    const [prixTotal,setPrixTotal] = useState(0)
    const [oldPt,setOldPt] = useState(0)

    

    const handleOut = ({currentTarget}) => {
        
       var prixtotal = fourniture.prix_unitaire * fourniture.quantite
        var valuereturn = prixtotal - oldPt
        ptot(valuereturn)
        setPrixTotal(prixtotal)
        setOldPt(prixtotal)
        changeFourniture(fourniture)
    }

 

    const handleChange =  ({currentTarget}) => {
        setFourniture({...fourniture,[currentTarget.name]:currentTarget.value})
    }
    return(
        <>
            <tr>
                <th>
                    <input type="text" name="nom" value={fourniture.nom} onChange={handleChange} onBlur={handleOut}></input>
                </th>
                <th>
                    <input type="text" name="quantite" value={fourniture.quantite} onChange={handleChange} onBlur={handleOut}></input>
                </th>
                <th>
                    <input type="text" name="prix_unitaire" value={fourniture.prix_unitaire} onChange={handleChange} onBlur={handleOut}></input>
                </th>
                <th>
                    <input type="text" name="pt" disabled value={prixTotal}></input>
                </th>
            </tr>
        </>
    )
}

export default FournitureRow