import React, { useEffect,useState } from 'react' 
import { Button } from 'react-bootstrap'
import PopUpVisuClient from '../clients/PopUpVisuClient'

 

const BiRowArchive = ({bi,restore}) => {
    
    const [openModClient, setOpenModClient] = useState(false)
    
    const openModalVisuClient = () => {
        setOpenModClient(true)
    }
   
    const closeModalVisuClient = () => {
        setOpenModClient(false)
    }


    return(
        <>
            <tr className="ligneTableau">
                <td>{bi.numero_bi}</td>
                <td>{bi.client[0].societe}<Button className="m-2 btnVisu" onClick={openModalVisuClient}><i className="tiny material-icons">remove_red_eye</i></Button></td>
                <td>{bi.machines.map(machine=><span key={machine.machine_id}>{machine.nom}</span>)}</td>
                {bi.bi_on_prod === 1 ? (<td><Button className="m-2 btnVisu"><i className="tiny material-icons">done</i></Button></td>) : (<td><Button className="m-2 btnDel"><i className="tiny material-icons">clear</i></Button></td>)}
                <td>
                    <Button className="m-2 btnDel" onClick={()=>restore(bi.bi_id)}><i className="material-icons">open_in_browser</i></Button>
                </td>
            </tr>
            <PopUpVisuClient open={openModClient} closeModalVisuClient={closeModalVisuClient} client={bi.client[0]}/>
        </>
    )
}

export default BiRowArchive