import React, {useState} from "react"
import { useEffect } from "react"
import { getUserById, updateUser } from "../../services/Users"
import Loading from "../../components/Loading"
import FormModifUser from "../../components/FormModifUser"
import { useHistory } from "react-router-dom"

const ContFormModifProfil = ({match}) => {
    const history = useHistory()
    const [user,setUser] = useState()
    const [loading, setLoading] = useState(true)
    useEffect(()=>{
        async function getUser(){
            let us = await getUserById(match.params.id)
            setUser(us[0])
            setLoading(false)
        }
        getUser()
    },[])

    const updatedUser = async (userUpdate) => {
       
        await updateUser(userUpdate)
        history.replace("/profil")
    }
    return loading ? (<Loading title={"chargement"} />) : (
        <>
            <FormModifUser user = {user} updatedUser={updatedUser}/>
        </>
    )
}

export default ContFormModifProfil