import React, { useEffect,useState } from 'react' 
import { Button } from 'react-bootstrap'
import PopUpForfaitClient from './PopUpForfaitClient'
import PopUpVisuClient from './PopUpVisuClient'
 

const NewClientRow = ({client,modifyClient,validClient}) => {
    
    return(
        <>
            <tr className="ligneTableau">
                <td>{client.societe}</td>
                <td>{client.interlocuteur}</td>
                <td>{client.numero_tel}</td>
                <td>{client.adresse_mail}</td>
                <td>
                    <Button className="m-2 btnVisu" onClick={()=>validClient(client.client_id)}><i className="tiny material-icons">add</i></Button>
                    <Button className="m-2 btnEdit" onClick={()=>{modifyClient(client)}}><i className="material-icons">edit</i></Button>
                </td>
            </tr>
        </>
    )
}

export default NewClientRow