import React, { useEffect,useState } from 'react' 
import { Button } from 'react-bootstrap'
import { getItem } from '../../services/localStorage'

 

const DocumentRow = ({document,delDocument,modifDoc}) => {
    const openDocument = () =>{
       const b64 = document.doc
       const linkSource = `data:application/pdf;base64,${b64}`;
        const downloadLink = document.createElement("a");
        const fileName = "vct_illustration.pdf";

        downloadLink.href = linkSource;
        downloadLink.download = fileName;
        downloadLink.click();
    }
    const modifyDocument = () => {
        modifDoc(document.document_id)
    }
    const deleteDocument = () => {
        delDocument(document.document_id)
    }
    return(
        <>
        {console.log(document)}
            <tr className="ligneTableau">
                <td>{document.nom}</td>
                <td>{document.type}</td>
                <td>{document.keyword}</td>
                <td>{document.created_at}</td>
                <td>{document.updated_at}</td>
                <td>
                <Button className="m-2 btnVisu"><a download={document.nom} href={"data:application/pdf;base64,"+document.doc}><i className="tiny material-icons">arrow_downward</i></a></Button>
                
                </td>
            </tr>
        </>
    )
}

export default DocumentRow