import React,{useState} from 'react' 
import Popup from 'reactjs-popup'
import { Form, Button } from 'react-bootstrap'
import { getNow } from '../assets/helpers/Date'
import { updatePassword } from '../services/Users'
import { useEffect } from 'react'
import Loading from './Loading'
import '../css/Loading.css'

const PopupMdp = ({open, utilisateur,modalMdpClose}) => {

    const [openMdp, setOpenMdp] = useState(open)
    const [errorMdp, setErrorMdp] = useState("")
    const [mdp, setMdp] = useState("")
    const [mdpConf, setMdpConf] = useState("")
    const [loading, setLoading] = useState(false)
    const closeModalMdp = () => {
        modalMdpClose()
    }
    const handleSubmitMdp = async (e) => {
        setErrorMdp("")
        e.preventDefault()
        if (mdp === "" || mdpConf === "" ){
            setErrorMdp("Mot de passe manquant")
            setMdp("")
            setMdpConf("")
            
        }else{
            if(mdp != mdpConf){
                setErrorMdp("les deux mots de passe ne corresponde pas")
                setMdp("")
                setMdpConf("")   
            }else{
                setLoading(true)
                let arrPass = {"id":utilisateur.user_id, "password" : mdp,"updated_at":getNow()}
                let resp = await sendPassword(arrPass)
                if(resp != 0){
                    closeModalMdp()
                    setMdp("")
                    setMdpConf("")
                    setErrorMdp("")
                }else{
                    setErrorMdp("Erreur de modification")
                    setMdp("")
                    setMdpConf("")
                }
            }
        }
        setLoading(false)
    }
   

    const sendPassword = async (arrPass) => {
        await updatePassword(arrPass)
    }


    const handleChangeMdp = ({currentTarget}) => {
        setMdp(currentTarget.value)
    }
    const handleChangeMdpConf = ({currentTarget}) => {
        setMdpConf(currentTarget.value)
    }

    useEffect(()=>{
        setOpenMdp(open)
    },[open])

    return (
        <>
            <Popup className="popUpMdp" open={openMdp} closeOnDocumentClick onClose={closeModalMdp}>
            <div className="containerMdp">
            {loading ? (<div className="loadingMdp"><Loading title={"Modification"} /></div>) : (<>
                <p>Changement de mot de passe</p>
                <Form className="formModifMdp" onSubmit={handleSubmitMdp}>
                    <Form.Group className="mb-3 titleMdp" controlId="mdp">
                        <Form.Label className="mb-3">Nouveau mot de passse</Form.Label>
                        <Form.Control type="password" placeholder="Mot de passe"  className="inputLogin" value={mdp ? mdp : ""} name="mdp" onChange={handleChangeMdp}/>
                    </Form.Group>

                    <Form.Group className="mb-3 titleMdp" controlId="mdpConf">
                        <Form.Label className="mb-3">Confirmation du mot de passe</Form.Label>
                        <Form.Control type="password" placeholder="Confirmation du mot de passe" value={mdpConf ? mdpConf : ""} className="inputLogin"  name="mdpConf" onChange={handleChangeMdpConf}/>
                    </Form.Group>
                    
                
                <span className="errorMdp">{errorMdp}</span>
                <div className="containerBouttonPopUp">
                    <Button type="submit">Valider</Button>
                    <Button onClick={closeModalMdp}>Annuler</Button>
                </div>
                
            </Form>
        </>)}</div>
    </Popup>
        </>
    )
}

export default PopupMdp