import React,{useState,useEffect} from 'react'
import { Table } from 'react-bootstrap'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import BiRow from '../../components/bi/BiRow'
import Loading from '../../components/Loading'
import { archiveBi, getAllBi, getBiWithUser } from '../../services/Bi'
import { getItem } from '../../services/localStorage'

const Bi = () =>{
    const [listBi,setListBi] = useState()
    const history = useHistory()
    const [loading,setLoading] = useState(false)
    useEffect(()=>{
        setLoading(true)
        getBi()
    },[])

    const getBi = async () => {
        var resp
        var lvl_user = getItem("userRole")
        
        if(lvl_user === "1"){
            
             resp = await getBiWithUser(getItem("userId"))
        }else{
             resp = await getAllBi()
             
        }
        
        setListBi(resp)
        setLoading(false)
    }
    const deleteBi = async (id) => {
        let resp = await archiveBi(id)
        if(resp === 1){
            setLoading(true)
            getBi()
        }
    }
    const modifyBi = (bi) => {
        history.push({pathname:"/modifyBi",state:{biModify:bi}})
    }
    return loading ? (<Loading title={"chargement"} />) : (
        <div className="containerMachine">
            <div className='containerTableUtilisateurs'>
            <div className="containerTable">
                    {console.log(listBi)}
                    {listBi != 0 ? (<Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Numero Bon Intervention</th>
                                <th>Client</th>
                                <th>Machine</th>
                                <th>Bi en prodution</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {listBi ? listBi.map(bi => <BiRow key={bi.bi_id} bi={bi} deleteBi={deleteBi} modifyBi={modifyBi} />) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>) : (<div className='containerNoBi'>Aucun Bi disponnible</div>)}
                </div>
            </div>
        </div>
    )
}

export default Bi