import React, { useEffect, useState } from 'react'
import { Button } from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import Popup from 'reactjs-popup'
import AjoutClient from '../components/clients/AjoutClient'
import VisualisationClient from '../components/clients/VisualisationClients'
import NewClientHome from '../components/home/NewClientHome'
import NewMachineHome from '../components/home/NewMachineHome'
import Loading from '../components/Loading'
import { getBiOnProd, getBiOnProdWithUser } from '../services/Bi'
import { getAllClients, getNewClient,updateClient } from '../services/Clients'
import { getItem } from '../services/localStorage'
import { getNewMachines, updateMachine } from '../services/Machines'
import '../css/Home.css'

const Home = () =>{
    const [loading,setLoading] = useState(false)
    const [listNewMachines,setListNewMachines] = useState([])
    const [listNewClients,setListNewClients] = useState([])
    const [listBiOnProd,setListBiOnProd] = useState([])
    const lvl_user = getItem("userRole")
    const[error,setError] = useState()
    const [openModalValid,setOpenModalValid] = useState(false)
    const [textModal,setTextModal] = useState()
    const history = useHistory()

    useEffect(()=>{
        setLoading(true)
        const getHome = async () => {
            if(lvl_user === "4" || lvl_user === "3"){
                await NewClients()
                await NewMachines()
                await AllBiOnProd()
            }else{
                
                await BiOnProd()
            }
            setLoading(false)
        }
        getHome()
    },[])

    const NewClients = async () => {
        let resp = await getNewClient()
        setListNewClients(resp)
          
    }

 

    const NewMachines = async () => {
        let resp = await getNewMachines()
        setListNewMachines(resp)
       
    }

    const BiOnProd = async () => {
        let resp = await getBiOnProdWithUser(getItem("userId"))
        setListBiOnProd(resp)
        
    }

    const AllBiOnProd = async () => {
        let resp = await getBiOnProd()
        setListBiOnProd(resp)
         
    }


       
    const validClient = async (id) => {
        var arrModifNewClient = {
            "client_id":id,
            "new_client":"0"
        }
        let resp = await updateClient(arrModifNewClient)
        if(resp === 1){
            setTextModal("Nouveau client ajouté avec succès")
            setOpenModalValid(true)
            NewClients()
        }else{
            setError("Erreur lors de l'ajout du nouveau client")
            setOpenModalValid(true)
        }
       
    } 

    const modifyClient = async (client) => {
        history.push("/newClient/"+client.client_id)
    }

    const closeModalValid = () => {
        setOpenModalValid(false)
    }

    const validMachine = async (id) => {
        var arrModifNewMachine = {
            "machine_id":id,
            "new_machine":"0"
        }
        let resp = await updateMachine(arrModifNewMachine)
        if(resp === 1){
            setTextModal("Nouvelle machine ajouté avec succès")
            setOpenModalValid(true)
            NewMachines()
        }else{
            setError("Erreur lors de l'ajout de la nouvelle machine")
            setOpenModalValid(true)
        }
    }

    const modifyMachine = (machine) => {
        history.push("/newMachine/"+machine.machine_id)
    }


    return loading ? (<Loading title={"chargement"} />) : (
        
        <div className="containerHome">
           
            {getItem('userRole') != 1 ? (<div className='containerNew'>{listNewClients !== 0 ? (<NewClientHome clientsList={listNewClients} validClient={validClient} modifyClient={modifyClient}/>) : (<div className='fontBold'>il n'y a pas de nouveaux clients a valider</div>)}</div>) : ""}
            {getItem('userRole') != 1 ? (<div className='containerNew'>{listNewMachines !== 0 ? (<NewMachineHome machineList={listNewMachines} validMachine={validMachine} modifyMachine={modifyMachine}/>) : (<div className='fontBold'>il n'y a pas de nouvelles machines</div>)}</div>) : ""}
            <div className='containerNew'>{listBiOnProd !== 0 ? (<>Il y a des bi non rentré</>) : (<div className="fontBold">il n'y a pas de bi en production</div>)}</div>
            <Popup open={openModalValid} closeOnDocumentClick onClose={closeModalValid}>
                <div className="containerClienPopUp">
                    <div>
                        <p>{!error ? textModal : error}</p>
                    </div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalValid}>Fermer</Button>
                    </div>
                </div>
            </Popup>
        </div>
    )
}

export default Home