import React,{useEffect, useState} from 'react' 
import Popup from "reactjs-popup";
import { Button } from 'react-bootstrap';

const PopUpVisuClient = ({open,client,closeModalVisuClient}) => {

    const [openModClient,setOpenModClient] = useState(false)

    useEffect(()=>{
        setOpenModClient(open)
    },[open])


    return(
        <Popup open={openModClient} closeOnDocumentClick onClose={closeModalVisuClient}>
                <div className="containerClienPopUp">
                    <p className='nomClient'>Client</p>                   
                    <div className="containerClientOnPopUp">
                        <div className='socityPopUp'>
                            <p><span className='fontName'>Societe :</span> {client.societe}</p>
                            <p><span className='fontName'>Interlocuteur :</span> {client.interlocuteur}</p>
                        </div>
                        <div className='popUpClientRow telClient'>
                            <p><span className='fontName'>Numero de téléphone :</span> {client.numero_tel}</p>
                            <p><span className='fontName'>Adresse mail :</span> {client.adresse_mail}</p>
                        </div>
                        <div className='popUpClientRow'><p><span className='fontName'>Adresse :</span> {client.adresse}</p></div>
                        <div className='popUpClientRow'>
                            <p><span className='fontName'>Code Postal :</span> {client.code_postal}</p>
                            <p><span className='fontName'>Ville :</span> {client.ville}</p>
                        </div>
                        <p>Créer le : {client.created_at} Modifier le : {client.updated_at}</p>
                    </div>
                    
                    <div className="dataUtilisateurPopUp">{client.data ? (client.data.map((d) =>  <p key={d.clientData_id}>{d.key} : {d.value}</p>)) : ("")}</div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalVisuClient}>Fermer</Button>
                    </div>
                </div>
            </Popup>
    )
}

export default PopUpVisuClient