import {BrowserRouter as Router, Route, Switch, Redirect} from 'react-router-dom';
import { useState } from 'react';
import Home from './pages/Home'
import Login from './pages/users/Login'
import NavBar from './components/NavBar'
import Profil from './pages/users/Profil';
import ContainerUtilisateurs from './pages/utilisateurs/ContainerUtilisateurs';
import ContainerMachines from './pages/machines/ContainerMachines';
import './css/App.css'
import Documents from './pages/documents/Documents';
import DocumentsAdd from './pages/documents/DocumentsAdd';
import Planning from './pages/plannings/Planning';
import Bi from './pages/bi/Bi';
import BiAdd from './pages/bi/BiAdd';
import {hasAutenticated, logout} from './services/Auth'
import Auth from './context/Auth';
import AuthenticatedRoute from './components/AuthenticatedRoute';
import Users from "./context/Users";
import { getItem } from './services/localStorage';
import FormModifProfil from './pages/users/ContainerFormModifProfil';
import ModifSignature from './pages/users/ModifSignature';
import ContainerUtilisateurArchive from './pages/utilisateurs/ContainerUtilisateurArchive';
import ContainerClient from './pages/clients/ContainerClient';
import ModifNewClient from './components/home/ModifNewClient';
import ModifNewMachine from './components/home/ModifNewMachine';
import MachineDocument from './pages/documents/MachineDocument';
import DocumentModify from './pages/documents/DocumentModify'
import BiModify from './pages/bi/BiModify';
import BiAddPicture from './pages/bi/BiAddPicture';
import BiArchive from './pages/bi/BiArchive';
import SignBi from './components/bi/SignBi';
import PdfBi from './pages/bi/PdfBi';
import VisuBi from './pages/bi/VisuBi';
import ModifyBi from './pages/bi/ModifyBi';
import BiModifyPicture from './pages/bi/BiModifyPicture';
import './css/App.css'
function App() {

  const [isAuthenticated, setIsAuthenticated] = useState(hasAutenticated())
  const [roleUser,setRoleUser] = useState(getItem("userRole"))
  return (
    <Auth.Provider value={{isAuthenticated, setIsAuthenticated}} >
      <Users.Provider value={{roleUser,setRoleUser}}>
      <Router>
        <NavBar />
        <div className="App container-fluid">
          <Switch>
            {!hasAutenticated() ? <Route exact path='/login' component={Login}/> : ""}
            <Route exact path='/' component={Login}/>
            <Route exact path='/login' component={Login}/>
            <AuthenticatedRoute path='/profil' component={Profil}/>
            <AuthenticatedRoute path='/home' component={Home}/>
            <AuthenticatedRoute path='/utilisateurs' component={ContainerUtilisateurs}/>
            <AuthenticatedRoute path='/utilisateur/add' component={ContainerUtilisateurs}/>
            <AuthenticatedRoute path='/utilisateur/archive' component={ContainerUtilisateurArchive}/>
            <AuthenticatedRoute path='/clients' component={ContainerClient}/>
            <AuthenticatedRoute path='/client/add' component={ContainerClient}/>
            <AuthenticatedRoute path='/client/modify' component={ContainerClient}/>
            <AuthenticatedRoute path='/client/archive' component={ContainerClient}/>
            <AuthenticatedRoute path='/newClient/:id' component={ModifNewClient} />
            <AuthenticatedRoute path='/machines' component={ContainerMachines}/>
            <AuthenticatedRoute path='/machine/add' component={ContainerMachines}/>
            <AuthenticatedRoute path='/machine/modify' component={ContainerMachines}/>
            <AuthenticatedRoute path='/machine/archive' component={ContainerMachines}/>
            <AuthenticatedRoute path='/newMachine/:id' component={ModifNewMachine} />
            <AuthenticatedRoute path='/documents' component={Documents}/>
            <AuthenticatedRoute path='/document/add' component={DocumentsAdd}/>
            <AuthenticatedRoute path='/document/modify/:id' component={DocumentModify}/>
            <AuthenticatedRoute path='/bis' component={Bi}/>
            <AuthenticatedRoute path='/bi/add' component={BiAdd}/>
            <AuthenticatedRoute path='/bi/archive' component={BiArchive}/>
            <AuthenticatedRoute path='/bi/modify' component={BiModify}/>
            <AuthenticatedRoute path='/bi/addPicture' component={BiAddPicture}/>
            <AuthenticatedRoute path='/bi/modifyPicture' component={BiModifyPicture}/>
            <AuthenticatedRoute path='/planning' component={Planning}/>
            <AuthenticatedRoute path='/modifProfil/:id' component={FormModifProfil}/>
            <AuthenticatedRoute path='/modifSignature/:id' component={ModifSignature}/>
            <AuthenticatedRoute path='/machineDocument/:id' component={MachineDocument}/>
            <AuthenticatedRoute path='/signBi' component={SignBi}/>
            <AuthenticatedRoute path='/visuBi' component={VisuBi}/>
            <AuthenticatedRoute path='/modifyBi' component={ModifyBi}/>
            
            
            <Redirect to="/login" />
          </Switch> 
        </div>
      </Router>
      </Users.Provider>
    </Auth.Provider>
  );
}

export default App;
