import React,{useEffect,useRef,useState} from "react";
import { useLocation } from "react-router-dom";
import { getItem } from "../../services/localStorage";
import { getUserById } from "../../services/Users";
import SignaturePad from "react-signature-canvas";
import { Button } from "react-bootstrap";
import { postBi } from "../../services/Bi";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import Loading from "../Loading";

const SignBi = ({match}) => {
    const [loading,setLoading] = useState(false)
    const history = useHistory()
    const location = useLocation();
    const [biOnProd,setBiOnProd] = useState(location.state.bi)
    const [signatureUser,setSignatureUser] = useState()
    const sigCanvas = useRef({});
    const [error,setError] = useState()
    useEffect(()=>{
        setLoading(true)
        getSign() 
    },[])

    const getSign = async () => {
        let resp = await getUserById(getItem("userId"))
        setSignatureUser(resp[0]['signature'])
        setLoading(false)
    }
    const send = async () => {
        var signClient = sigCanvas.current.getTrimmedCanvas().toDataURL("image/png");
        var allJour = []
        biOnProd.forfait.map((jour)=>{
            allJour.push(jour)
        })
        
        var forf = [
            {"forfait_client":allJour
            }
        ]
        console.log(biOnProd)
        var machineToSend = []
        machineToSend.push({"machine_id":biOnProd.machine.machine_id})
        var biSend = {
            "numero_bi" : biOnProd.numero_bi,
            "travail_a_faire" : biOnProd.travail_a_faire,
            "numero_bl" : biOnProd.numero_bl,
            "constatation" : biOnProd.constatation_arrivee,
            "travail_effectue" : biOnProd.travaux_realise,
            "signature_client" : signClient,
            "bi_on_prod" : 0,
            "user_id" : getItem("userId"),
            "modeForfait_id" : 3,
            "client_id" : biOnProd.client_id,
            "fournitures":biOnProd.fourniture,
            "machines":machineToSend,
            "pictures":biOnProd.pictures,
            "forfait":forf,
            "data":[]

        }
        console.log(biSend)
        let resp = await postBi(biSend)
        if(resp === 0){
            setError("Erreur lors de l'ajout du BI")
        }else if(resp ===1){
            setError("Bi deja créer")
        }else{
            history.push('/bis')
        }
    }
    const clearSignature = () => {
        sigCanvas.current.clear();
    }
    return loading ? (<Loading title={"chargement"} />) : (
        <>
           <div className="containerBi">
            <div className="FormAjoutBi">
                <div className="inputSignBi">
                    <p className="fontBold fontTitle mt-3">Numero de Bi : {biOnProd.numero_bi}</p>
                </div>
                <div className="inputSignBi">
                    <p><span className='fontBold'>Societe :</span> {biOnProd.client.societe}</p>
                    <p><span className='fontBold'>Interloccuteur:</span> {biOnProd.client.interlocuteur}</p>
                </div>
                <div className="inputSignBi">
                    <p><span className='fontBold'>Machine :</span> {biOnProd.machine.nom}</p>
                    <p><span className='fontBold'>Numero de série:</span> {biOnProd.machine.numero_serie}</p>
                </div>
                <div className="inputSignBiCol">
                    <p><span className='fontBold'>Travaux à réaliser :</span></p><p> {biOnProd.travail_a_faire}</p>
                </div>
                
                <div className="inputSignBiCol">
                    <p><span className='fontBold'>Constatation a l'arrivée :</span></p><p> {biOnProd.constatation_arrivee}</p>
                </div>
                <div className="inputSignBiCol">
                    <p><span className='fontBold'>Travaux effectué :</span></p><p> {biOnProd.travaux_realise}</p>
                </div>
               
                {biOnProd.numero_bl !== "" ? (
                    <div className="inputSignBiCol">
                        <p ><span className='fontBold'>Numéro de BL :</span> {biOnProd.numero_bl}</p>
                        {biOnProd.fourniture.length !== 0 ? (
                            biOnProd.fourniture.map((four)=>(
                                <div key={four.index} className='pieceSignBi'>
                                    <p className="fontBold">piece {four.index + 1}</p><p><span className='fontBold'>Designation :</span> {four.nom} <span className='fontBold'>Quantité :</span> {four.quantite} <span className='fontBold'>Prix unitaire :</span> {four.prix_unitaire}€</p>
                                </div>
                            ))
                        ):""}
                        <p ><span className="fontBold">Prix global Fourniture :</span> {biOnProd.prix_total_fourniture}€</p>
                    </div>
               ) : <div className="inputSignBiCol"><p className="fontBold">Aucune fourniture</p></div>}
                {biOnProd.prix_total_forfait !== 0 ? (
                    <div className="inputSignBiCol mt-4">
                        <p className="fontBold">Main d'oeuvre et déplacement :</p>
                            {biOnProd.forfait.length !== 0 ?(
                                biOnProd.forfait.map((data)=>(
                                    <>
                                    <div key={data.date}><p ><span className="fontBold">{data.jour}</span> : {data.date}</p></div>
                                    <div key={data.jour} className='forfaitSignBi'>
                                        <p><span className="fontBold">Nombre d'heure de déplacement :</span> {data.nombre_deplacement}</p>&nbsp;
                                        <p><span className="fontBold">Nombre d'heure de travail :</span> {data.nombre_heure}</p>&nbsp;
                                        <p><span className="fontBold">Nombre de nuit d'hotel :</span> {data.hotel}</p>&nbsp;
                                        <p><span className="fontBold">Nombre de repas :</span> {data.repas}</p>&nbsp;
                                    </div></>
                                ))
                            ) : ""}
                        <p><span className="fontBold">Total general main d'oeuvre et déplacement :</span> {biOnProd.prix_total_forfait} €</p>
                    </div>
               ) : (<div><p>Aucun frai</p></div>)}
               <div>
                   {signatureUser ? (<div className="containerSignatureSignBi mt-3 mb-3"><div className="mt-3 mb-3">
                       <p>Signature Technicien : </p>
                       <img src={signatureUser} />
                       

                   </div><div><p>Signature Client : </p><SignaturePad ref={sigCanvas} canvasProps={{ className: "signatureCanvas" }} /></div><Button onClick={clearSignature}>Supprimer Signature</Button></div>) : ""}
               </div>
               <div>
                   <Button className="mb-3 mt-3" onClick={send}>Envoyer</Button>
               </div>
               {error ? (<div className="containerRowFormClientError">
                    <p>{error}</p>
                    </div>) : ""}
            </div>
            </div>
        </>
    )
}

export default SignBi