import { getItem, addItem, removeItem } from "./localStorage";
import jwt_decode from "jwt-decode";

export function hasAutenticated(){
    const token = getItem('tokenUser');
    const IsValid = token ? tokenIsValid(token) : false;

    if(false === IsValid){
        removeItem('tokenUser')
    }
    return IsValid;
}

export async function login(credentials){
   let token = await fetch('https://www.emanorcloud.fr/api/v2/login', {
        method: 'POST',
        mode: "cors",
        body: JSON.stringify({"login": credentials.login, "password": credentials.password}),
        headers: {'Content-Type': 'application/json'}})
        .then(response => response.json())
    if(token.message != "Unauthorized"){
        await addItem('tokenUser',token.token)
    }else{
        return false
    }
    return true
}

export function logout(){
    removeItem('tokenUser')
    removeItem('userId')
    removeItem('userRole')
}

function tokenIsValid(token){
    const {exp} = jwt_decode(token);

    if(exp *1000 > new Date().getTime()){
        return true
    }else{
        return false
    }
}

export async function setUserLog(token){
    const decodeToken = jwt_decode(token);
    addItem('userId',decodeToken.sub);
    const role = await getRoleUser(localStorage.getItem("userId"))
    addItem('userRole',role[0].lvlUser_id)
}

export async function getRoleUser(id){
    return await fetch("https://www.emanorcloud.fr/api/v2/users/search/" + id, {
        mode: "cors",
        method: 'GET',
        headers: {'Authorization': "bearer " + localStorage.getItem('tokenUser')}
    })
        .then(response => response.json())
}