import React,{useEffect,useRef,useState} from "react";
import { Button } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import Moment from 'react-moment';

const VisuBi = () => {
    const location = useLocation();
    const [bi,setBi] = useState(location.state.biVisu)
    const [prixTtFourniture,setPrixTtFourniture] = useState(0)
    const [prixTtForfait,setPRixTtForfait] = useState(0)
    const [forfaitClient,setForfaitClient] = useState(bi.forfait["forfait_client"][0])
    useEffect(()=>{
        var prixTotalFourniture = 0
        bi.fournitures.map(four=>{
            prixTotalFourniture = prixTotalFourniture + (four.quantite * four.prix_unitaire)
        })
        var prixTotalForfait = 0
        bi.forfait["detail_forfait_client"].map((forfait)=>{
            prixTotalForfait = prixTotalForfait + (forfait.nombre_deplacement * forfaitClient.forfait_dep) + (forfait.nombre_heure * forfaitClient.forfait_heure) + (forfait.repas * forfaitClient.repas) + (forfait.hotel * forfaitClient.hotel)
        })
        setPrixTtFourniture(prixTotalFourniture)
        setPRixTtForfait(prixTotalForfait)
    },[])
    const history = useHistory()
    const retour = () => {
        history.push("/bis")
    }
    return(
        <> 
        
        <div className="containerVisuBi">
        <div className="VisuBi">
            <p className="fontTitleRowVisuBi mt-5">Bi N° {bi.numero_bi}</p>
            <div className="rowVisuBi mt-5">
                <div className='clientVisuBi'>
                    <p className="fontTitleRowVisuBi">Client</p>
                    <p><span className="fontBold">Societe :</span> {bi.client[0].societe}</p>
                    <p><span className="fontBold">Interlocuteur :</span> {bi.client[0].interlocuteur}</p>
                </div>
                <div className='clientVisuBi'>
                    <p className="fontTitleRowVisuBi">Machine</p>
                    <p><span className="fontBold">Constructeur :</span> {bi.machines[0].constructeur}</p>
                    <p><span className="fontBold">nom :</span> {bi.machines[0].nom}</p>
                    <p><span className="fontBold">numero de serie :</span> {bi.machines[0].numero_serie}</p>
                </div>
            </div>
            <div className='clientVisuBi mt-3'>
                <p className="fontTitleRowVisuBi">Travaux a Réaliser </p>
                <p>{bi.travail_a_faire}</p>
               
            </div>
            {bi.fournitures.length !== 0 ? (
                <div className='clientVisuBi mt-4'>
                    <p className="fontTitleRowVisuBi">Fournitures</p>
                    {bi.fournitures.map((four)=>(
                        <div className='borderBottom' key={four.fourniture_id}>
                            <p><span className="fontBold">nom :</span> {four.nom} <span className="fontBold">quantite :</span> {four.quantite} <span className="fontBold">prix unitaire :</span> {four.prix_unitaire} <span className="fontBold">total :</span> {(four.quantite * four.prix_unitaire)} €</p>
                        </div>
                    ))}
                    <p className="fontTitleRowVisuBi">Total fourniture : {prixTtFourniture} €</p>
                </div>
                ) : ""}
                {bi.forfait["detail_forfait_client"].length !== 0 ? (
                <div className='ForfaitVisuBi mt-4'>
                    <p className="fontTitleRowVisuBi">Forfait</p>
                    {bi.forfait["detail_forfait_client"].map((forfait)=>(
                        <div className="dateBi" key={forfait.indemniteForfaitClient_id}>
                            <p className="fontBold">{forfait.jour} <Moment format="DD/MM/YYYY">{forfait.date}</Moment></p>
                            <p> <span className="fontBold">Nombre d'heure de déplacement :</span> {forfait.nombre_deplacement} <span className="fontBold">Nombre d'heure de travail :</span> {forfait.nombre_heure} <span className="fontBold">Nombre de repas :</span> {forfait.repas} <span className="fontBold">Nombre de nuits d'hotel :</span> {forfait.hotel}</p>
                        </div>
                    ))}
                    <p className="fontTitleRowVisuBi mb-5">Total forfait : {prixTtForfait} €</p>
                </div>
                ) : ""}
                <div className="rowVisuBi ">
                <div className='clientVisuBi mb-5'>
                
                <p className="fontTitleRowVisuBi">Constation a l'arrivée </p>
                <p>{bi.constatation}</p>
                </div>
                <div className='clientVisuBi mb-5'>
                <p className="fontTitleRowVisuBi">Travaux effectivement réalisés </p>
                <p>{bi.travail_effectue}</p>
                
                </div>
                </div>
                <div className="rowVisuBi ">
                    <div className='clientVisuBi'>
                        <span className="fontBold mb-3">Signature technicien : </span><img src={bi.signature_user} />
                    </div>
                        <div className='clientVisuBi'>
                            <span className="fontBold mb-3">Signature Client : </span><img src={bi.signature_client} />
                    </div>
                </div>
                {bi.pictures.length !== 0 ? (
                    <div className="clientVisuBi mt-5 ">
                        <p className="fontTitleRowVisuBi mb-5">Photos liées au Bi </p>
                        {bi.pictures.map((photo)=>(
                            <div className="mb-5" key={photo.picture_id}>
                                
                                <img src={photo.path} width={'400px'}/>
                            </div>
                        ))}
                    </div>
                ) : ""}
                <div className="containerButtonLogin mb-5">
                    <Button onClick={retour}>Retour</Button>
                </div>
        </div>
        </div>
        </>
    )
}

export default VisuBi