import React from 'react'
import { Card, Button } from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import '../css/Profile.css'
const Signature = ({user, edit=true}) => {
    const history = useHistory()
    const handleSignature = (e) => {
        e.preventDefault()
        history.replace("/modifSignature/"+user.user_id)
    }
    return(
        <div className="signatureCard mb-4">
            <Card.Title className="text-center mb-4">Signature</Card.Title>
            <Card.Img className="mb-4" src={user ? user.signature : ""} alt="my signature"/>
        </div>
    )
}

export default Signature