import { Button } from 'react-bootstrap';
import React, {useState,useEffect} from 'react'
import { FloatingLabel, Form } from 'react-bootstrap';
import { useLocation } from "react-router-dom";
import Loading from '../../components/Loading';
import { getAllClients } from '../../services/Clients';
import { getMachineByClient } from '../../services/Machines';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { updateBi, updateMachineBi } from '../../services/Bi';

const ModifyBi = () => {
    const location = useLocation();
    const history = useHistory()
    const [bi,setBi] = useState(location.state.biModify)
    const [loading, setLoading] = useState(false)
    const [listClient,setListClient] = useState()
    const [listMachine,setListMachine] = useState([])
    const [machineSelect,setMachineSelect] = useState()
    const [clientSelectId,setClientSelectId] = useState(bi.client[0].client_id)
    const [openListMachine,setOpenListMachine] = useState(true)
    const [error,setError] = useState()
    const [biModify,setBiModify] = useState({
        "bi_id" : bi.bi_id,
        "client_id" : bi.client[0].client_id,
        "numero_bi" : bi.numero_bi,
        "machine_id":""+bi.machines[0].machine_id,
        "travail_a_faire":bi.travail_a_faire,
        "constatation" : bi.constatation,
        "travail_effectue" : bi.travail_effectue
    })
    useEffect(()=>{
        setLoading(true)
        
        getListClient()
        getMachine()
    },[])

    useEffect(()=>{
        getMachine()
    },[clientSelectId])

    const getMachine = async () => {
        if(clientSelectId){
            let resp = await getMachineByClient(clientSelectId)
            if(resp === 0){
                setListMachine([])
                setOpenListMachine(true)
            }else{
                setListMachine(resp)
                setOpenListMachine(false)
            }
           
        } 
    }


    const getListClient = async () => {
        let resp = await getAllClients()
        setListClient(resp)
        setLoading(false)
    }

    const handleChangeSelectClient = (e) => {
        setMachineSelect()
        setListMachine([])
        setClientSelectId(e.target.value)
        setBiModify({...biModify,['client_id']:e.target.value})
    }

    const handleChangeSelectMachine = (e) => {
        setMachineSelect(e.target.value)
        setBiModify({...biModify,['machine_id']:e.target.value})
    }

    const handleChangeTravauxARealiser = (e) => {
        setBiModify({...biModify,['travail_a_faire']:e.target.value})
    }

    const handleChangeConstatation = (e) => {
        setBiModify({...biModify,['constatation']:e.target.value})
    }

    const handleChangeTraveauRealise = (e) => {
        setBiModify({...biModify,['travail_effectue']:e.target.value})
    }

    const annul = () => {
        history.push("/bis")
    }

    const modifier = async () => {
      
        var biToSend = {
            "bi_id":biModify.bi_id,
            "travail_a_faire":biModify.travail_a_faire,
            "constatation":biModify.constatation,
            "travail_effectue":biModify.travail_effectue
        }
        let resp = await updateBi(biToSend)
        
        var biMachineModify = -1
        if(biModify.machine_id !== ""+bi.machines[0].machine_id){
            biMachineModify = biModify.machine_id
        }
        if(biMachineModify === 0){
            setError("veuillez selectionner une machine")
        }
        if(biMachineModify !== -1){
            var machineBiToSend = {
                "bi_id":biModify.bi_id,
                "machine_id":biModify.machine_id
            }
            let resp2 = await updateMachineBi(machineBiToSend)
           
        }
        if(resp === 0 || resp === 1){
            setError("Erreur lors de la modification du Bi")
        }else{
            history.push("/bis")
        }
        
    }

    return loading ? (<Loading title={'chargement'}/>) : (
        <>
        <div className="containerBi">
           
           
            
                <Form className="FormModifyBi"> 
                <div className='mt-5'>
                <p className='fontBold fontTitle'>Modification du bi : {bi.numero_bi}</p>
            </div>
                <div>{listClient ? 
                        (
                            <Form.Group className="  mb-3" controlId="client">
                                <select value={biModify.client_id} className="selectClient" onChange={handleChangeSelectClient} disabled={true}>
                                    <option key='0' value='0'>client</option>
                                    {listClient.map((client) => (
                                    <option key={client.client_id} value={client.client_id}>{client.societe}</option>))}
                                </select>
                            </Form.Group>
                        ) : "" }
                        <Form.Group className="mb-3" controlId="machine">
                            <select value={biModify.machine_id} className="selectClient" onChange={handleChangeSelectMachine} disabled={openListMachine}>
                                <option key='0' value='0'>Machine</option>
                                {listMachine.map((machine) => (
                                <option key={machine.machine_id} value={machine.machine_id}>{machine.nom}</option>))}
                            </select>
                        </Form.Group>
                    </div>
                    <div className="travailAFaire mt-3">
                        <FloatingLabel controlId="travail_a_faire" label="Travaux à réaliser" className="mb-3">
                            <Form.Control as="textarea" className="textAreaTravail" placeholder="Travaux à réaliser" value={biModify.travail_a_faire} onChange={handleChangeTravauxARealiser}/>
                        </FloatingLabel>
                    </div>
                    <div className="travailAFaire">
                        <FloatingLabel controlId="constatation_arrive" label="Constatation a l'arrivée" className="mb-3">
                            <Form.Control as="textarea" className="textAreaTravail" placeholder="Constatation a l'arrivée" value={biModify.constatation} onChange={handleChangeConstatation}/>
                        </FloatingLabel>
                    </div>
                    <div className="travailAFaire">
                        <FloatingLabel controlId="travaux_realise" label="Travaux effectivement réalisés" className="mb-3">
                            <Form.Control as="textarea" className="textAreaTravail" placeholder="Travaux effectivement réalisés" value={biModify.travail_effectue} onChange={handleChangeTraveauRealise}/>
                        </FloatingLabel>
                    </div>
                    {error ? (<div className="containerRowFormClientError">
                    <p>{error}</p>
                    </div>) : ""}
                    <div className="travailAFaire mb-5">
                        <Button className="m-2" onClick={annul}>Annuler</Button>
                        <Button className="m-2" onClick={modifier}>Modifier</Button>
                    </div>
                </Form>
            </div>
        
        </>
    )
}

export default ModifyBi