
import React, { useEffect, useState } from 'react'
import { Button, Form } from 'react-bootstrap'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import Popup from 'reactjs-popup'
import Loading from '../../components/Loading'
import { postDocumentMachine } from '../../services/Documents'
import { getAllMachines } from '../../services/Machines'
import '../../css/Machines.css'

const MachineDocument = ({match}) =>{
    const [listMachine,setListMachine] = useState()
    const [idDocument,setIdDocument] = useState(match.params.id)
    const history = useHistory()
    var hobbies = [];
    const [loading,setLoading]= useState(false)
    const [error,setError] = useState()
    const [openModalValid,setOpenModalValid] = useState(false)
    useEffect(()=>{
        setLoading(true)
        getMahcines()
    },[])



    const getMahcines = async () => {
        let resp = await getAllMachines()
        setListMachine(resp)
        setLoading(false)
    }

    const handleInputChange = (event) =>{
        const target = event.target; 
        var value = target.value;
        
        if(target.checked){
            hobbies[value] = value;   
        }else{
            var index = hobbies.indexOf(value)
            
            if(index !== -1){
                hobbies.splice(index, 1);
            }
        }
        
    }

   
    const submit = async () => {
        
        var arrMachineDocument = {}
        var resp = 1
        hobbies.map(async (id)=>{
            arrMachineDocument = {
                "machine_id" : id,
                "document_id" : idDocument
            }
            var resp2 = await postDocumentMachine(arrMachineDocument)
            if(resp2 === 0){
                resp = 0
            }
        })
        if(resp === 1){
            setOpenModalValid(true)
        }else{
            setError("erreur lors de l'ajout document machine")
        }
    }

    const closeModalValid = () => {
        setOpenModalValid(false)
        history.push("/documents")
    }
        return loading ? (<Loading title={"chargement"}/>) : (
            <div className="containerMachine">
                <div className="row formAjoutMachine containerDocumentMachine">
                    <div className="">
                        <br /><br />
                        
                        <div className="form-row ">
                            <div className="form-group containerDocumentMachine mb-4">
                            <label className='fontTitle'>Liste des machines :</label><br />
                                <div className='checkMachineDocument'>
                                
                                {listMachine ? ( listMachine.map((machine)=>(
                                    <div className="form-check form-check-inline mb-2" key={machine.machine_id}>
                                    <input className="form-check-input" type="checkbox" name="hobbies" value={machine.machine_id} onChange={handleInputChange} />
                                    <label className="form-check-label" htmlFor="inlineCheckboxh1">{machine.nom}</label>
                                    </div>
                                ))
                                ) : ""} 
                            </div>
                           </div>
                        </div>
                        <div className="form-row">
                            <div className="col-md-12 text-center mb-5">
                                <button type="submit" className="btn btn-primary" onClick={submit}>Envoyer</button>
                            </div>
                        </div>
                        
                    </div>
                </div>
                {error ? (<div className="containerRowFormClientError">
                                <p>{error}</p>
                            </div>) : ""}
                            <Popup open={openModalValid} closeOnDocumentClick onClose={closeModalValid}>
                <div className="containerClienPopUp">
                    <div>
                        <p>Document et machine lié avec succès</p>
                    </div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalValid}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            </div>
        )  
}

export default MachineDocument