import React,{useEffect, useState} from 'react' 
import Popup from "reactjs-popup";
import { Button, Form } from 'react-bootstrap';
import Loading from '../Loading';
import { updateClient } from '../../services/Clients';
import { isNumb } from '../../assets/helpers/general';

const PopUpForfaitClient = ({openForfaitClient,client,closeModalForfaitClient,creation}) => {

    const [openModalForfaitClient,setOpenModalForfaitClient] = useState(false)
    const [forfait,setForfait] = useState({
        "client_id":"",
        "forfait_dep":"",
        "forfait_heure":"",
        "hotel":"",
        "repas":""
    })
    const [edit,setEdit] = useState(true)
    const [loading, setLoading] = useState(false)
    const [error,setError] = useState()
    const [openValid,setOpenValid] = useState(false)
    useEffect(()=>{
        setForfait({
            "client_id":client.client_id ? client.client_id : "",
            "forfait_dep":client.forfait_dep,
            "forfait_heure":client.forfait_heure,
            "hotel":client.hotel,
            "repas":client.repas
        })
        if(creation){
            setEdit(false)
        }
    },[])

    useEffect(()=>{
        setOpenModalForfaitClient(openForfaitClient)
    },[openForfaitClient])

    const handleChange = ({currentTarget}) => {
        setForfait({...forfait,[currentTarget.name]:currentTarget.value})
    }

    const editFormForfait = () => {
        setEdit(false)
    }

    const annulFormForfait = () => {
        setError('')
        if(!edit){
            setEdit(true)
            setForfait({
                "client_id":client.client_id ? client.client_id : "",
                "forfait_dep":client.forfait_dep,
                "forfait_heure":client.forfait_heure,
                "hotel":client.hotel,
                "repas":client.repas
            })
            setOpenModalForfaitClient(false)
        }else{
            setOpenModalForfaitClient(false)
            setForfait({
                "client_id":client.client_id ? client.client_id : "",
                "forfait_dep":client.forfait_dep,
                "forfait_heure":client.forfait_heure,
                "hotel":client.hotel,
                "repas":client.repas
            })
        }
        if(creation){
            setEdit(false)
        }
    }

    const sendModifForfait = async () => {
            setLoading(true)
            setError('')
            if(!isNumb(forfait.forfait_dep) || forfait.forfait_dep === ""){
                setError("Veuillez entrer un chiffre seulement")
               
            }
            else if(!isNumb(forfait.forfait_heure || forfait.forfait_heure === "")){
                setError("Veuillez entrer un chiffre seulement")
               
            }
            else if(!isNumb(forfait.hotel) || forfait.hotel === ""){
                setError("Veuillez entrer un chiffre seulement")
               
            }
            else if(!isNumb(forfait.repas)  || forfait.repas === ""){
                setError("Veuillez entrer un chiffre seulement")
               
            }else{ 
                let resp = await updateClient(forfait)
                if(resp != 1){
                    setError("Erreur lors de la modification du forfait")
                }else{
                    client.forfait_dep = forfait.forfait_dep
                    client.forfait_heure = forfait.forfait_heure
                    client.hotel = forfait.hotel
                    client.repas = forfait.repas
                    setOpenModalForfaitClient(false)
                    setEdit(true)
                    setOpenValid(true)
                }
            }
            
            setLoading(false)
        }
    

    const closeModalValid = () => {
        setOpenValid(false)
    }

    const setCrea = () => {
        setError('')
        if(!isNumb(forfait.forfait_dep) || forfait.forfait_dep === ""){
            setError("Veuillez entrer un chiffre seulement")
           
        }
        else if(!isNumb(forfait.forfait_heure || forfait.forfait_heure === "")){
            setError("Veuillez entrer un chiffre seulement")
           
        }
        else if(!isNumb(forfait.hotel) || forfait.hotel === ""){
            setError("Veuillez entrer un chiffre seulement")
           
        }
        else if(!isNumb(forfait.repas)  || forfait.repas === ""){
            setError("Veuillez entrer un chiffre seulement")
           
        }else{
        client.forfait_dep = forfait.forfait_dep
        client.forfait_heure = forfait.forfait_heure
        client.hotel = forfait.hotel
        client.repas = forfait.repas
        setForfait({
            "client_id":client.client_id ? client.client_id : "",
            "forfait_dep":"",
            "forfait_heure":"",
            "hotel":"",
            "repas":""
        })
        setOpenModalForfaitClient(false)
        }
    }

    return( 
        <>
            <Popup open={openModalForfaitClient} closeOnDocumentClick onClose={closeModalForfaitClient} className='forfaitClientPopUp'>
                
                <Form className="formModifForfaitClient">    
                    <Form.Group className="mb-3" controlId="forfait_dep">
                        <Form.Label  className="mb-3">Forfait déplacement</Form.Label>
                        <Form.Control type="text" disabled={edit} placeholder="Forfait déplacement" value={forfait.forfait_dep} name="forfait_dep" onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="forfait_Heure">
                        <Form.Label  className="mb-3">Forfait heure</Form.Label>
                        <Form.Control type="text" disabled={edit} placeholder="Forfait heure" value={forfait.forfait_heure} name="forfait_heure" onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="hotel">
                        <Form.Label  className="mb-3">Hotel</Form.Label>
                        <Form.Control type="text" disabled={edit} placeholder="Hotel" value={forfait.hotel} name="hotel" onChange={handleChange}/>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="repas">
                        <Form.Label  className="mb-3">Repas</Form.Label>
                        <Form.Control type="text" disabled={edit} placeholder="Repas" value={forfait.repas} name="repas" onChange={handleChange}/>
                    </Form.Group>
                    <div className="errorLogin"><p>{error}</p></div>
                    <div className="buttonForfait">
                        <Button onClick={annulFormForfait}>Fermer</Button>
                    
                    </div>
                </Form>
            </Popup>
            <Popup open={openValid} closeOnDocumentClick onClose={closeModalValid}>
                <div className='containerMdp'><p>Forfait modifié avec succès</p>
                <Button className='buttonValidForfaitClient' onClick={closeModalValid}>Fermer</Button></div>
            </Popup>
        </>
    )
}

export default PopUpForfaitClient