import React from 'react'
import { Table } from 'react-bootstrap'
import ClientRow from '../clients/ClientRow'
import NewClientRow from '../clients/NewClientRow'


const NewClientHome = ({clientsList,validClient,modifyClient}) => {
  
    return(
        <>
        <div className="containerTable">
                    <Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Société</th>
                                <th>Interlocuteur</th>
                                <th>Numéro de téléphone</th>
                                <th>Adresse mail</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {clientsList ? clientsList.map(client => <NewClientRow key={client.client_id} client={client} modifyClient={modifyClient} validClient={validClient}/>) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>
                </div>
        </>
    )
}

export default NewClientHome