import React, {useContext, useEffect, useState} from 'react'
import {Button, Form} from 'react-bootstrap'
import '../../css/FormLogin.css'
import logo from '../../assets/img/logo.png'
import Auth from '../../context/Auth'
import { login, logout, setUserLog } from '../../services/Auth'
import  Users  from '../../context/Users'
import Loading from '../../components/Loading'

const Login = ({history}) =>{

    const {isAuthenticated, setIsAuthenticated} = useContext(Auth)
    const {roleUser, setRoleUser} = useContext(Users)
    const [error, setError] = useState("")
    const [user,setUser] = useState({
        login:"",
        password:""
    })
    const [loading, setLoading] = useState(false)

    const handleChange = ({currentTarget}) => {
        const {name, value} = currentTarget
        setUser({...user,[name]: value})
    }

    const handleSubmit = async (e) => {
        
        e.preventDefault();
        if(user.login === ""|| user.password === ""){
            setLoading(false)
            setError("Erreur de login et/ou de mot de passe")
        }else{
            setLoading(true)
            try{
                const response = await login(user)
                setIsAuthenticated(response)
                if(response === true){
                    await setUserLog(localStorage.getItem("tokenUser"))
                    setRoleUser(localStorage.getItem("userRole"))
                    history.replace("/home")
                }else{
                    setLoading(false)
                    setUser({
                        login:"",
                        password:""
                    })
                    setError("Erreur de login et/ou de mot de passe")
                }
            }catch({response}){
               
            }
        }
    }

    useEffect(()=>{
        if (isAuthenticated){
            history.replace("/home")
        }else{
            logout()
            history.replace("/login")
        }
    },[])

    return loading ? (<Loading title={"connexion"}/>) : (
        <div className="container-fluid">
            <div className="containerLogin">
            <div className="logoLogin"><img src={logo} width="100%"/></div><div className="barreLogin"></div>
            <div className="containerFormLogin">
                <Form className="formLogin" onSubmit={handleSubmit}>
                    <Form.Group className="mb-3 titleLogin" controlId="login">
                        <Form.Label className="mb-3">Login</Form.Label>
                        <Form.Control type="text" placeholder="Login" className="inputLogin" name="login" onChange={handleChange}/>
                    </Form.Group>
                    
                    
                    <Form.Group className="mb-3 titleLogin" controlId="password">
                        <Form.Label  className="mb-3">Password</Form.Label>
                        <Form.Control type="password" placeholder="Password"className="inputLogin" name="password" onChange={handleChange}/>
                    </Form.Group>
                    <div  className="errorLogin">{error ? error : ""}</div>
                    <div className="containerButtonLogin mt-4 ">
                        <Button type="submit" className="buttonMdp">
                            Mot de passe oublié ?
                        </Button>
                        <Button type="submit"  className="buttonConnexion">
                            Connexion
                        </Button>
                    </div>
                </Form>
            </div>
            </div>
        </div>
    )
}

export default Login