export async function postDocument(arrDocument){
    return await fetch('https://www.emanorcloud.fr/api/v2/documents', {
             method: 'POST',
             mode: "cors",
             body : JSON.stringify(arrDocument),
             headers: {'Content-Type': 'application/json', 'Authorization': 'bearer ' + localStorage.getItem('tokenUser')}})
             .then(response => {
                     if(response.status === 400){
                             return 0
                     }else if(response.status === 409){
                            return -1
                     }
                     else{
                             return response.json()
                     }
                })
}

