import React,{useState} from "react";
import { useEffect } from "react";
import { Table } from "react-bootstrap";
import { getAllMachines,getAllArchiveMachines,archiMachine,updateMachine } from "../../services/Machines";
import Loading from "../Loading";
import '../../css/Machines.css'
import MachineRow from "./MachineRow";
import { Button } from 'react-bootstrap'
import Popup from 'reactjs-popup'
import { getNow } from '../../assets/helpers/Date'

const VisualisationMachine = ({param,modifMachine}) => {
    const [loading,setLoading] = useState(false)
    const [machines,setMachines] = useState()
    const [message,setMessage] = useState("")
    const [openModalConfirm,setOpenModalConfirm] = useState(false)
    const [textModalConfirm,setTextModalConfirm] = useState('')

    useEffect(()=>{
        setLoading(true)
        if(param != "archive"){
            getMachines()
            
        }else{
            console.log("archive")
            getArchiveMachines()
        }
        
    },[])

    const getMachines = async () =>{
        let resp = await getAllMachines()
        if(resp != 0){
            setMachines(resp)
            setLoading(false)
        }else{
            setMessage("aucune machine")
            setLoading(false)
        }
    }

    const getArchiveMachines = async () => {
        let resp = await getAllArchiveMachines()
        if(resp != 0){
            setMachines(resp)
            setLoading(false)
        }else{
            setMessage("Aucune machine archivée")
            setLoading(false)
        }
    }

    const archiveMachine = async (id) => {
        let resp = await archiMachine(id)
        if(resp == 1){
            setTextModalConfirm("Machine archiver avec succès")
            setOpenModalConfirm(true)
        }else{
            setTextModalConfirm("Erreur lors de l'archivage de la machine")
            setOpenModalConfirm(true)
        }
        getMachines()
    }

    const closeModalConfirm = () => {
        setOpenModalConfirm(false)
    }

    const desarchiveMachine = async (id) => {
        let arrUpdateMachine={
            "machine_id":id,
            "updated_at":getNow(),
            "deleted_at":null
        }
        let resp = await updateMachine(arrUpdateMachine)
        if(resp == 1){
            setTextModalConfirm("Machine restaurer avec succès")
            setOpenModalConfirm(true)
            getArchiveMachines()
        }else{
            setTextModalConfirm("Erreur lors de la restauration de la machine")
            setOpenModalConfirm(true)
        }
    }

    return loading ? (<Loading title={"chargement"}/>) : (
        <div className='containerTableUtilisateurs'>
            {message !== "" ? (<div className="containerArchiveClient"><p className='fontBold fontTitle'>{message}</p> </div>)
            : (
                <div className="containerTable">
                    <Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Nom</th>
                                <th>Constructeur</th>
                                <th>Numéro de série</th>
                                <th>Type</th>
                                <th>Client</th>
                                <th>Créer le</th>
                                <th>Dernière modification</th>
                                {param === "archive" ? <th>Archivé le</th> : null}
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                             {machines ? machines.map(machine => <MachineRow key={machine.machine_id} machine={machine} archiveMachine={archiveMachine} desarchiveMachine={desarchiveMachine} modifMachine={modifMachine}/>) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>
                </div>
            )}
            <Popup open={openModalConfirm} closeOnDocumentClick onClose={closeModalConfirm}>
                <div className="containerClienPopUp">
                    <div>
                        <p className="fontTitle">{textModalConfirm}</p>
                    </div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalConfirm}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            
        </div>
    )
}

export default VisualisationMachine