import React, { useState } from 'react'
import { Button, Form } from 'react-bootstrap'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import Popup from 'reactjs-popup'
import { postDocument } from '../../services/Documents'

const DocumentsAdd = () =>{
    const [document,setDocument] = useState({
        "nom":"",
        "keyword":"",
        "type":"",
        "file":""
    })
    const history = useHistory()
    const [insertId,setInsertId] = useState()
    const [error,setError] = useState()
    const [openModalValid,setOpenModalValid] = useState(false)
    const handleFileChange = async (e) => {
       const file = e.target.files[0]
       const base64 = await convertBase64(file)
       setDocument({...document,["file"]:base64})
       setFileOk(false)
    }

    const [fileOk,setFileOk] = useState(true)

    const handleChange = ({currentTarget}) => {
        setDocument({...document,[currentTarget.name]:currentTarget.value})
    }

    const convertBase64 = (file) => {
        return new Promise((resolve,reject)=>{
            const fileReader = new FileReader()
            fileReader.readAsDataURL(file)

            fileReader.onload = () => {
                resolve(fileReader.result)
            }

            fileReader.onerror = (error) => {
                reject(error)
            }
        })
    }

    const handleAnnul = () => {
        setDocument({
            "nom":"",
            "keyword":"",
            "type":"",
            "file":""
        })
        setError("")
        setFileOk(true)
    }

    const handleKeywordChange = ({currentTarget}) => {
            var keyword = currentTarget.value
            keyword = keyword.replace(" ",",")
            setDocument({...document,[currentTarget.name]:keyword})
    }

    const sendDocument = async () => {
        setError("")
        
        if(document.nom === "" || document.keyword === "" || document.type === "" || document.file === ""){
            setError("Tous les champs doivent être rempli")
        }else{
            let resp = await postDocument(document)
            if (resp === -1){
                setError("Le document est déja existant")
            }else if (resp === 0){
                setError("Erreur lors de l'ajout du document")
            }else{
                setInsertId(resp)
                setOpenModalValid(true)
            }
        }
    }

    const closeModalValid = () => {
        setOpenModalValid(false)
    }

    const goToMachineDocument = () => {
        
        history.push("/machineDocument/"+insertId)
    }

    return(
        <div className="containerMachine">
            <Form className="formAjoutMachine ">
            <div className="containerRowFormMachine mt-5 mb-3">
                    <div className="columnFormMachine">
                        <p className="titleModifMachine">Ajout document technique</p>
                    </div>
                </div>
                <Form.Group className="mb-3" controlId="nom">
                    <Form.Label  className="mb-3 titleInputMachine">Nom du document</Form.Label>
                    <Form.Control type="text" placeholder="Nom du document" value={document ? document.nom : ""} name="nom" onChange={handleChange}/>
                </Form.Group>
                <Form.Group className="mb-3" controlId="nom">
                    <Form.Label  className="mb-3 titleInputMachine">Type</Form.Label>
                    <Form.Control type="text" placeholder="type" value={document ? document.type : ""} name="type" onChange={handleChange}/>
                </Form.Group>
                <Form.Group className="mb-3" controlId="keyword">
                    <Form.Label  className="mb-3 titleInputMachine">Mots clés</Form.Label>
                    <Form.Control type="text" placeholder="Mots clés" value={document ? document.keyword : ""} name="keyword" onChange={handleKeywordChange}/>
                </Form.Group>
                <Form.Group controlId="formFile" className="mb-3">
                    <Form.Label className='titleInputMachine'>Document</Form.Label>
                    <Form.Control type="file" accept="application/pdf" onChange={(e)=>handleFileChange(e)}/>
                </Form.Group>
                    {error ? (<div className="containerRowFormClientError">
                                <p>{error}</p>
                            </div>) : ""}
                <div className="containerRowFormClientButton mb-3 mt-3">
                    <div className="columnFormClientButton">
                        <Button onClick={handleAnnul}>Annuler</Button>
                        <Button onClick={sendDocument} disabled={fileOk}>Ajouter</Button>
                    </div>
                </div>
            </Form>
            <Popup open={openModalValid} onClose={closeModalValid}>
                <div className="containerClienPopUp">
                    <div>
                        <p className="fontTitle">Document ajouté avec succès</p>
                        <p className="fontTitle">Voulez-vous lier des machines a ce document</p>
                    </div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalValid}>Non</Button>
                        <Button onClick={goToMachineDocument}>Oui</Button>
                    </div>
                </div>
            </Popup>
        </div>
        
    )
}

export default DocumentsAdd