import React from 'react'
import {Card, Button, Form, FormGroup} from 'react-bootstrap'
import '../css/Profile.css'
import Popup from "reactjs-popup";
import { useState } from 'react'
import Loading from './Loading'
import { useEffect } from 'react'
import { getItem } from '../services/localStorage'
import { getRole, updatePassword } from '../services/Users'
import { useHistory } from 'react-router-dom'
import Signature from './Signature';
import { getNow } from '../assets/helpers/Date';
import PopupMdp from './PopupMdp';

const CardProfil = ({users, modifProfil}) => {
    const history = useHistory()
    const [user,setUser] = useState({})
    const [loading, setLoading] = useState(true)
    const [role, setRole] = useState("")
    const [openModalMdp, setOpenModalMdp] = useState(false);
    
    const [error, setError] = useState()

    const [valid,setValid] = useState("")

    useEffect(() => {
        users ? setUser(users) : setUser()
    },[users])
    
    useEffect(()=> {
        if (!user) {
            setLoading(true)
        }else{
            setRole(getRole(getItem("userRole")))
            setLoading(false)
        } 
    },[user])
    
 
    
   
    const openMdp = () => {
        setOpenModalMdp(true)
    }

    const closeMdp = () => {
        setOpenModalMdp(false)
    }
    

    

    return loading ? (<Loading title={"chargement"}/>) : (
        <><Card>
            <Card.Header><span className="titleCardProfile">{role}</span></Card.Header>
            <Card.Body>
                <Card.Title className="mb-3">Nom de famille : <span className='textCard'>{user ? user.lastname : ""}</span></Card.Title>
                <Card.Title className="mb-3">Prénom : <span className='textCard'>{user ? user.firstname : ""}</span></Card.Title>
                <Card.Title className="mb-3">Login : <span className='textCard'>{user ? user.login : ""}</span></Card.Title>
                <Signature user={user} />
                
            </Card.Body>
        </Card>
        <PopupMdp open={openModalMdp} utilisateur={user} modalMdpClose={closeMdp}/></>
    )
}

export default CardProfil