import React from 'react'
import { Table } from 'react-bootstrap'

import NewMachineRow from './NewMachineRow'


const NewMachineHome = ({machineList,validMachine,modifyMachine}) => {
  
    return(
        <>
        <div className="containerTable">
                    <Table responsive>
                        <thead>
                            <tr className="ligneTableau">
                                <th>Nom</th>
                                <th>Constructeur</th>
                                <th>Numéro de série</th>
                                <th>Type</th>
                                <th>Client</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {machineList ? machineList.map(machine => <NewMachineRow key={machine.machine_id} machine={machine} modifyMachine={modifyMachine} validMachine={validMachine}/>) : <tr key={"1"}><td></td></tr>}
                        </tbody>
                    </Table>
                </div>
        </>
    )
}

export default NewMachineHome