import React, {useContext, useEffect, useState} from 'react'
import { Container,Nav,Navbar, NavDropdown, Button} from 'react-bootstrap'
import { useHistory } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import '../css/NavBar.css'
import Auth from '../context/Auth'
import Users from "../context/Users";
import { getItem } from '../services/localStorage';
import { logout, setUserLog } from '../services/Auth';

const NavBar = () =>{
    const { isAuthenticated, setIsAuthenticated} = useContext(Auth);
    const { roleUser, setRoleUSer} = useContext(Users);
    const history = useHistory();
    const [lvlUser,setLvlUser] = useState(99)
    
    const handleButtonClick = (e) => {
        e.preventDefault();
        if(!isAuthenticated){
            history.push("/login")
        }else{
            handleLogout()   
        }
    }
    const handleLogout = () => {
        logout();
        setIsAuthenticated(false)
        setLvlUser(0)
    }

    useEffect(()=>{
        setLvlUser(3)
    },[isAuthenticated])
    
    return(
        <div>
            <Navbar className="NavBar" expand="xl">
                <Container fluid>
                    <Navbar.Brand href="/">Emanor BI</Navbar.Brand>
                    <Navbar.Toggle aria-controls="navbarScroll" />
                    <Navbar.Collapse id="navbarScroll">
                        <Nav
                        className="me-auto my-2 my-lg-0"
                        style={{ maxHeight: '100px' }}
                        navbarScroll
                        >
                            
                        { (isAuthenticated && (
                            <>
                            <Nav.Link href="/"><span className="linkNavBar">Accueil</span></Nav.Link>   
                            <Nav.Link href="/profil"><span className="linkNavBar">Profil</span></Nav.Link>
                            
                            <NavDropdown title="Clients" id="navbarScrollingDropdown">
                                <NavDropdown.Item href="/clients"><span className="linkNavBar">Visualiser</span></NavDropdown.Item>
                            </NavDropdown>
                            <NavDropdown title="Machines" id="navbarScrollingDropdown">
                                <NavDropdown.Item href="/machines"><span className="linkNavBar">Visualiser</span></NavDropdown.Item>
                            </NavDropdown>
                            <NavDropdown title="Documents Techniques" id="navbarScrollingDropdown">
                                <NavDropdown.Item href="/documents"><span className="linkNavBar">Visualiser</span></NavDropdown.Item>
                            </NavDropdown>
                            <NavDropdown title="Bons d'intervention" id="navbarScrollingDropdown">
                                <NavDropdown.Item href="/bis"><span className="linkNavBar">Visualiser</span></NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="/bi/add"><span className="linkNavBar">Créer</span></NavDropdown.Item>

                            </NavDropdown>
                            </>
                            )) || ""}
                        </Nav>
                        {(!isAuthenticated) ? <Button className="buttonNavBar buttonLogin" onClick={handleButtonClick}>Log in</Button> : (
                            <Button className="buttonNavBar " onClick={handleLogout}>Deconnexion</Button>
                        )}
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </div>
    )
}

export default NavBar