import React,{useState} from "react";
import { useEffect } from "react";
import { Button, Form } from 'react-bootstrap'
import { useHistory } from "react-router-dom";
import Popup from "reactjs-popup";
import { getNow } from "../../assets/helpers/Date";
import { getAllClients } from "../../services/Clients";
import { updateMachine } from "../../services/Machines";
import Loading from "../Loading";

const ModifyMachine = ({machineModify}) => {
    const [machine,setMachine] = useState(machineModify)
    const [error,setError] = useState()
    const [clients,setClients] = useState([])
    const history = useHistory()
    const [loading,setLoading] = useState(false)
    const [clientOnMachine,setClientOnMachine] = useState("0")
    const [openPopUpValid,setOpenPopUpValid] = useState(false)
    useEffect(()=>{
        setLoading(true)
        setError("")
        const getClients = async () => {
            let resp = await getAllClients()
            setClients(resp)
            setLoading(false)
        }
        getClients()
    },[])

   useEffect(()=>{
        if(clients){
            clients.map((client)=>{
                if(client.client_id === machineModify.client_id){
                    setClientOnMachine(client.client_id)
                }
            })
        }
   },[clients,machineModify])

    const handleChange = ({currentTarget})=>{
        setMachine({...machine,[currentTarget.name]:currentTarget.value})
    }
    const handleAnnul = () => {
        setMachine(machineModify)
        history.push("/machines")
    }
    const sendMachine = async () => {
        setError("")
        var machineModif = {...machine,"updated_at":getNow()}
        delete machineModif.data
        if(machineModif.nom === "" || machineModif.constructeur === "" || machineModif.numero_serie === "" ||
        machineModif.type === ""){
            setError("Tout les champs doivent etre rempli")
        }else if (machineModif.client_id === "0" ){
            setError('Un client doit être sélectionné')
        }else{
            let resp = await updateMachine(machineModif)
            if(resp == 1){
                setOpenPopUpValid(true)
            }else{
                setError("Erreur lors de la modification de la machine")
            }
        }
        
    }

    const handleChangeSelect = (e) => {
        setClientOnMachine(e.target.value)
        setMachine({...machine,["client_id"]:e.target.value})
    }

    const closePopUpValid = () => { 
        setOpenPopUpValid(false)
        history.push("/machines")
    }

    return loading ? ( <Loading title={"chargement"} />) : (
        <>
        <Form className="formAjoutMachine">
                <div className="containerRowFormMachine">
                    <div className="columnFormMachine">
                        <p className='titleModifMachine'>Modification Machine</p>
                    </div>
                </div>
                <div className="containerRowFormMachine">
                    <div className="columnFormMachine">
                        <Form.Group className="mb-3" controlId="nom">
                            <Form.Label  className="mb-3 titleInputMachine">Nom</Form.Label>
                            <Form.Control type="text" placeholder="Nom" value={machine ? machine.nom : ""} name="nom" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                    <div className="columnFormMachine">
                        <Form.Group className="mb-3" controlId="constructeur">
                            <Form.Label  className="mb-3 titleInputMachine">Constructeur</Form.Label>
                            <Form.Control type="text" placeholder="constructeur" value={machine ? machine.constructeur : ""} name="constructeur" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                </div>
                <div className="containerRowFormMachine">
                    <div className="columnFormMachine">
                    <Form.Group className="mb-3" controlId="numero_serie">
                        <Form.Label  className="mb-3 titleInputMachine">Numéro de série</Form.Label>
                        <Form.Control type="text" placeholder="Numéro de série" value={machine ? machine.numero_serie : ""} name="numero_serie" onChange={handleChange}/>
                    </Form.Group>
                    </div>
                    <div className="columnFormMachine">
                        <Form.Group className="mb-3" controlId="type">
                            <Form.Label  className="mb-3 titleInputMachine">Type</Form.Label>
                            <Form.Control type="text" placeholder="Type" value={machine ? machine.type : ""} name="type" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                    {clients ? (<div className="selectClientMachine">
                        <p className="titleInputMachine">Client</p>
                        <Form.Group className="mb-3 " controlId="client">
                            <select value={clientOnMachine} onChange={handleChangeSelect}>
                                <option key='0' value='0'>client</option>
                            {clients.map((client) => (
                                <option key={client.client_id} value={client.client_id}>{client.societe}</option>
                            ))}
                            </select>
                        </Form.Group>
                    </div> ) : "" }
                </div>
                {error ? (<div className="containerRowFormMachineError">
                    <p>{error}</p>
                </div>) : ""}
                <div className="containerRowFormMachineButton">
                    <div className="columnFormMachineButton">
                        <Button onClick={handleAnnul}>Annuler</Button>
                        <Button onClick={sendMachine}>Modifier</Button>
                    </div>
                </div>
            </Form>
            <Popup open={openPopUpValid} closeOnDocumentClick onClose={closePopUpValid}>
                <div className="containerClienPopUp">
                    <p className="fontTitle">Machine modifié avec succès</p>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closePopUpValid}>Fermer</Button>
                    </div>
                </div>
            </Popup>
        </>
        
    )
}

export default ModifyMachine