import React, { useEffect, useState } from 'react'
import { Form, Button } from 'react-bootstrap'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import Popup from 'reactjs-popup'
import { getNow } from '../../assets/helpers/Date'
import Loading from '../../components/Loading'
import { getOneDocument, updateDocument } from '../../services/Documents'

const DocumentModify = ({match}) => {
    const id = match.params.id
    const [error,setError] = useState()
    const [document,setDocument] = useState()
    const [loading,setLoading] = useState(false)
    const [openModalValid,setOpenModalValid] = useState(false)
    const history = useHistory()
    useEffect(()=>{
        setLoading(true)
        getDocument()
    },[])

    const getDocument = async () => {
        let resp = await getOneDocument(id)
        setDocument(resp[0])
        setLoading(false)
    }
    const handleChange = ({currentTarget}) => {
        setDocument({...document,[currentTarget.name]:currentTarget.value})
    }
    const handleKeywordChange = ({currentTarget}) => {
        var keyword = currentTarget.value
        keyword = keyword.replace(" ",",")
        setDocument({...document,[currentTarget.name]:keyword})
    }
    const handleAnnul = () => {
        history.push("/documents")
    }
    const modDocument = async () => {
        var arrUpdateDocument = {}
        if(document.nom.includes(".pdf")){
            arrUpdateDocument = {
                "document_id":document.document_id,
                "nom" : document.nom,
                "type":document.type,
                "keyword":document.keyword, 
                "updated_at":getNow()
            }
        }else{
            arrUpdateDocument = {
                "document_id":document.document_id,
                "nom" : document.nom+".pdf",
                "type":document.type,
                "keyword":document.keyword,
                "updated_at":getNow()
            }
        }
        let resp = await updateDocument(arrUpdateDocument)
        if(resp === 1){
            setOpenModalValid(true)
        }else{
            setError("erreur lors de la modification du document")
        }
    }
    const closeModalValid = () => {
        setOpenModalValid(false)
        history.push("/documents")
    }
    return loading  ? (<Loading title={"chargement"} /> ) : (
        <>
        <div className="containerMachine">
     
            <Form className="formAjoutMachine ">
            <div className="containerRowFormMachine mt-5 mb-3">
                    <div className="columnFormMachine">
                        <p className="titleModifMachine">Modification document technique</p>
                    </div>
                </div>
            <div className="containerRowFormClient ">
                    <div className="columnFormClient">
                        <Form.Group className="mb-3 " controlId="nom">
                            <Form.Label  className="mb-3 titleInputMachine">Nom</Form.Label>
                            <Form.Control type="text" placeholder="Nom" value={document ? document.nom : ""} name="nom" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                </div>
                <div className="containerRowFormClient">
                    <div className="columnFormClient">
                        <Form.Group className="mb-3" controlId="type">
                            <Form.Label  className="mb-3 titleInputMachine">Type</Form.Label>
                            <Form.Control type="text" placeholder="type" value={document ? document.type : ""} name="type" onChange={handleChange}/>
                        </Form.Group>
                    </div>
                </div>
                <div className="containerRowFormClient">
                    <div className="columnFormClient">
                        <Form.Group className="mb-3 titleInputMachine" controlId="keyword">
                            <Form.Label  className="mb-3">Mots clés</Form.Label>
                            <Form.Control type="text" placeholder="Mots clés" value={document ? document.keyword : ""} name="keyword" onChange={handleKeywordChange}/>
                        </Form.Group>
                    </div>
                </div>
                {error ? (<div className="containerRowFormClientError">
                                <p>{error}</p>
                            </div>) : ""}
                <div className="containerRowFormClientButton mb-3 mt-3">
                    <div className="columnFormClientButton">
                        <Button onClick={handleAnnul}>Annuler</Button>
                        <Button onClick={modDocument} >Modifier</Button>
                    </div>
                </div>
            </Form>
            <Popup open={openModalValid} closeOnDocumentClick onClose={closeModalValid}>
                <div className="containerClienPopUp">
                    <div>
                        <p className="fontTitle">Document modifié avec succès</p>
                    </div>
                    <div className="containerBouttonPopUp">
                        <Button onClick={closeModalValid}>Fermer</Button>
                    </div>
                </div>
            </Popup>
            </div>
        </>
    )
}

export default DocumentModify 